<template>
    <div class="index-top-bar">
            <div class="index-top-bar-search">
                <router-link to="/" class="index-top-bar-logo">
                    <div><i class="fa fa-cube fa-lg"></i> 闲置</div>
                </router-link>
            </div>
            <div class="index-top-bar-search-container">
                <div class="index-top-bar-search-icon">
                    <i class="fa fa-search fa-lg"></i>
                </div>
                <input type="text" placeholder="输入想要搜索的内容" class="index-search-input" v-model="searchText">
                <div class="index-top-bar-search-filter">
                    <div class="filter-title">筛选</div>
                    <div class="filter-select">

                        <div class="filter-institute">
                            <div v-for="(value, key) in searchInstitute" :key="key">
                                <input type="checkbox" v-model="searchInstitute[key].select" />
                                {{ value.name }}
                            </div>
                        </div>
                        
                        <div class="underline"></div>

                        <div class="filter-classification">
                            <div v-for="(value, key) in searchClassification" :key="key" >
                                <input type="checkbox" v-model="searchClassification[key]" />
                                {{ value.name }}
                            </div>
                        </div>
                    </div>
                </div>
                <div class="index-top-bar-search-button" @click="search">搜索</div>
            </div>
            <div class="index-top-bar-button" v-if="loginStatus">
                <div class="index-top-bar-head-container">
                    <img :src="headIcon">
                </div>
                <router-link class="button" tag="div" :to="{path: '/mine', query: {publishPageStart: 0, buyPageStart: 0, mode: 'publish'}}">我的</router-link>
                <router-link class="button" tag="div" to="message">
                    <div class="unread-container">
                        消息
                        <span class="unread" v-if="unreadMessageNum != 0">4</span>
                    </div>
                </router-link>
                <div class="button" @click="userLogout">退出</div>
                <router-link class="button" tag="div" to="publish">发布</router-link>
            </div>






            <div class="index-top-bar-button" v-if="!loginStatus">
                <div @click="loginShow" class="button">登陆</div>
                <div class="button" @click="registerShow">注册</div>
            </div>
            <div id="login" v-if="loginShowStatus">
                    <div class="close">
                        <i class="fa fa-close fa-lg close-icon" @click="loginShow"></i>
                    </div>
                    <div class="warning" v-if="warningLoginUsernameStatus">{{warningLoginUsernameText}}</div>
                    <input type="text" placeholder="用户名" class="form-input" v-model="username" autofocus>
                    <div class="warning" v-if="warningLoginPasswordStatus">{{warningLoginPasswordText}}</div>
                    <input type="password" placeholder="密码" class="form-input" v-model="password" @keyup.enter="userLogin">
                    <div id="loginSubmit" @click="userLogin">登陆</div>
                </div>

                <div id="register" v-if="registerShowStatus">
                    <div class="close">
                        <i class="fa fa-close fa-lg close-icon" @click="registerShow"></i>
                    </div>
                    <div class="warning" v-if="warningRegisterNameStatus">{{warningRegisterNameText}}</div>
                    <input type="text" placeholder="姓名" class="form-input" v-model="registerName">
                    <div class="warning" v-if="warningRegisterStudentNoStatus">{{warningRegisterStudentNoText}}</div>
                    <input type="text" placeholder="学号" class="form-input" v-model="registerStudentNo">
                    <div class="warning" v-if="warningRegisterPhoneStatus">{{warningRegisterPhoneText}}</div>
                    <input type="text" placeholder="手机号" class="form-input" v-model="registerPhone">
                    <select name="xueyuan" id="xueyuan" v-model="registerInstitute">
                        <option value="" selected disabled style="display: none">请选择学院</option>
                        <option v-for="(value, key) in institute" :key="key" :value="key">{{ value }}</option>
                    </select>

                    <div class="underline"></div>

                    <div class="warning" v-if="warningRegisterUsernameStatus">{{warningRegisterUsernameText}}</div>
                    <input type="text" placeholder="用户名" class="form-input" v-model="registerUsername">
                    <div class="warning" v-if="warningRegistePasswordStatus">{{warningRegisterPasswordText}}</div>
                    <input type="password" placeholder="输入密码" class="form-input" v-model="registerPassword">
                    <div class="warning" v-if="warningRegisteRepeatPasswordStatus">{{warningRegisterRepeatPasswordText}}</div>
                    <input type="password" placeholder="重复密码" class="form-input" v-model="regitsterRepeatPassword" @keyup.enter="userRegister">
                    <div id="loginSubmit" @click="userRegister">注册</div>
                </div>
        </div>
</template>

<script>
import Vue from 'vue'
export default {
    data() {
        return {
            headIcon: localStorage.getItem("avatar"),

            username : "",
            password : "",
            registerName : "",
            registerStudentNo: "",
            registerPhone: "",
            registerInstitute: "",
            registerUsername: "",
            registerPassword : "",
            regitsterRepeatPassword : "",
            loginStatus: false,
            loginShowStatus: false,
            registerShowStatus: false,

            institute: {},

            warningLoginUsernameStatus: true,
            warningLoginPasswordStatus: true,
            warningRegisterNameStatus: true,
            warningRegisterStudentNoStatus: true,
            warningRegisterPhoneStatus: true,
            warningRegisterUsernameStatus: true,
            warningRegistePasswordStatus: true,
            warningRegisteRepeatPasswordStatus: true,

            warningLoginUsernameText: "请填写用户名",
            warningLoginPasswordText: "请填写密码",
            warningRegisterNameText: "请填写姓名",
            warningRegisterStudentNoText: "请填写学号",
            warningRegisterPhoneText: "请填写手机号",
            warningRegisterUsernameText: "请填写用户名",
            warningRegisterPasswordText: "请填写密码",
            warningRegisterRepeatPasswordText: "请重复密码",

            unreadMessageNum: 0,
            classifications: [],

            searchInstitute: [],
            searchClassification: [],

            searchText: "",

        }
    },
    methods: {
        getInstitute () {
            let url = '/index/institute';
            let data = "";
            this.myAjax_get(url, data).then(response => {
                if(response.flag == 1) {
                    this.institute = response.data;
                    for(let i in response.data) {
                        let temp = {
                            name: response.data[i],
                            id: i,
                            select: true
                        };
                        this.searchInstitute.push(temp);
                    }
                }
            }).catch(error => console.log(error));
        },
        getClassification () {
            this.myAjax_get('/index/classification').then(response =>{
                if(response.flag == 1) {
                    for(let i in response.data) {
                        this.classifications.push(response.data[i]);
                        let temp = {
                            name: response.data[i].name,
                            id: response.data[i].id,
                            select: true
                        };
                        this.searchClassification.push(temp);
                    }
                }
            });
        },
        userLogin () {
            let url = '/login';
            let data = {
                username : this.username,
                password : this.password,
            }
            this.myAjax_post(url, data).then(response => {
                if(response.flag == 1) {
                    this.loginStatus = true;
                    this.loginShowStatus = false;
                    this.setCookie('username', response.data.username, 3600);
                    this.setCookie('admin', response.data.admin, 3600);
                    this.setCookie('userid', response.data.id, 3600);
                    this.setCookie('login', 'true', 3600);
                    localStorage.setItem('avatar', response.data.avatar);
                    this.$router.go(0);
                }
                else {
                    this.warningLoginUsernameStatus = true;
                    this.warningLoginUsernameText = response.msg;
                }
            }).catch(error => console.log(error));
        },
        userLogout () {
            let url = '/logout';
            let data = "";
            this.myAjax_get(url, data).then(response => {
                if(response.flag == 1){
                    this.unsetCookie('login');
                    this.unsetCookie('username');
                    this.unsetCookie('admin');
                    this.unsetCookie('userid');
                    this.checkLogin();
                    this.$emit("login", "false");
                    localStorage.clear();
                    this.$router.go(0);
                }
                });
        },
        search () {
            if(this.searchText != "") {
                let totalInstitute = [];
                let totalClassification = [];
                for(let i in this.searchInstitute) {
                    if(this.searchInstitute[i].select == true) {
                        totalInstitute.push(this.searchInstitute[i].id);
                    }
                }
                for(let i in this.searchClassification) {
                    if(this.searchClassification[i].select == true) {
                        totalClassification.push(this.searchClassification[i].id);
                    }
                }
                // const url2 = '/index/getSearchPages';
                // const data2 = {
                //     search: this.searchText,
                //     institute: totalInstitute.join(','),
                //     classification: totalClassification.join(','),
                //     pageSize: this.constant.searchPageSize,
                // }
                // this.myAjax_post(url2, data2).then(response => {

                // });

                this.$router.push({path: '/search', query: {
                    search: this.searchText,
                    institute: totalInstitute.join(','),
                    classification: totalClassification.join(','),
                    pageStart: 0,
                    pageSize: this.constant.searchPageSize,
                    }
                })
            }
            else {
                alert('搜索关键词不能为空');
            }
        },
        userRegister () {
            let url = '/register';
            let data = {
                name: this.registerName,
                stuId: this.registerStudentNo,
                phone: this.registerPhone,
                instituteId: this.registerInstitute,
                username: this.registerUsername,
                password: this.registerPassword,
                email: "emmmm",
            }
            for(let i in data) {
                if(data[i] == "" || data[i] ==undefined) {
                    return false;
                }
            }
            this.myAjax_post(url, data).then(response => {
                if(response.flag == 1) {
                    alert(response.msg);
                    this.$router.push({path: '/index'});
                }
                else {
                    alert(response.msg);
                }
            });
        },
        // getUnreadMessageNum () {
        //     const url = '/user/myPrivateMessage';
        //     const data = {
        //         userId: this.getCookie('userid'),
        //     };
        //     this.myAjax_post(url, data).then(response => this.unreadMessageNum = response.data.length);
        // },
        checkLogin () {
            this.loginStatus = this.checkCookie('login');
        },
        loginShow (){
            this.registerShowStatus = false;
            this.loginShowStatus = !this.loginShowStatus;
        },
        registerShow (){
            this.loginShowStatus = false;
            this.registerShowStatus = !this.registerShowStatus;
        }
    },
    created () {
        this.getInstitute();
        this.getClassification();
        if(this.checkLogin()) {
            // this.getUnreadMessageNum();
        }
    },
    watch: {
        registerPhone () {
            let check = /^1[0-9]{10}$/.test(this.registerPhone);
            if(check == false) {
                this.warningRegisterPhoneStatus = true;
                this.warningRegisterPhoneText = '请填写正确的手机格式';
            }
            else {
                this.warningRegisterPhoneStatus = false;
                this.warningRegisterPhoneText = "";
            }
        },
        regitsterRepeatPassword () {
            let check = (this.registerPassword == this.regitsterRepeatPassword);
            if(check == false) {
                this.warningRegisteRepeatPasswordStatus = true;
                this.warningRegisterRepeatPasswordText = "两次输入的密码不一致";   
            }
            else {
                this.warningRegisteRepeatPasswordStatus = false;
                this.warningRegisterRepeatPasswordText = "";
            }
        },
        registerName () {
            if(this.registerName == "") {
                this.warningRegisterNameStatus = true;
                this.warningRegisterNameText = "请填写姓名";
            }
            else {
                this.warningRegisterNameStatus = false;
                this.warningRegisterNameText = "";
            }
        },
        registerStudentNo () {
            if(this.registerStudentNo == "") {
                this.warningRegisterStudentNoStatus = true;
                this.warningRegisterStudentNoText = "请填写学号";
            }
            else {
                this.warningRegisterStudentNoStatus = false;
                this.warningRegisterStudentNoText = "";
            }
        },
        registerStudentNo () {
            if(this.registerStudentNo == "") {
                this.warningRegisterStudentNoStatus = true;
                this.warningRegisterStudentNoText = "请填写学号";
            }
            else {
                this.warningRegisterStudentNoStatus = false;
                this.warningRegisterStudentNoText = "";
            }
        },
        registerUsername () {
            if(this.registerUsername == "") {
                this.warningRegisterUsernameStatus = true;
                this.warningRegisterUsernameText = "请填写用户名";
            }
            else {
                this.warningRegisterUsernameStatus = false;
                this.warningRegisterUsernameText = "";
            }
        },
        registerPassword () {
            if(this.registerPassword == "") {
                this.warningRegistePasswordStatus = true;
                this.warningRegisterPasswordText = "请填写用户名";
            }
            else {
                this.warningRegistePasswordStatus = false;
                this.warningRegisterPasswordText = "";
            }
        },
        username () {
            if(this.username == "") {
                this.warningLoginUsernameStatus = true;
                this.warningLoginUsernameText = "请填写用户名"
            }
            else {
                this.warningLoginUsernameStatus = false;
                this.warningLoginUsernameText = "";
            }
        },
        password () {
            if(this.password == "") {
                this.warningLoginPasswordStatus = true;
                this.warningLoginPasswordText = "请填写密码";
            }
            else {
                this.warningLoginPasswordStatus = false;
                this.warningLoginPasswordText = "";
            }
        }
    }
}
</script>

<style scoped>
.index-top-bar {
    height: 70px;
    width: 100%;
    background: rgba(197, 60, 94, 1);
    display: flex;
    justify-content: space-between;
    align-items: center;
    box-sizing: border-box;
    padding: 0 20px;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 20;
}
.index-top-bar a:link,
.index-top-bar a:visited,
.index-top-bar a:hover,
.index-top-bar a:active {
    color: white;
    text-decoration: none;
}
.index-top-bar-logo {
    height: 70px;
    width: 100px;
    text-align: center;
    line-height: 70px;
    color: white;
    font-size: 22px;
    text-decoration: none;
}
.index-top-bar-search {
    height: 100%;
    width: 100px;
    margin-right: 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.index-top-bar-search-container {
    max-width: 600px;
    min-width: 400px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 20px;
}
.index-top-bar-search-icon {
    height: 42px;
    width: 42px;
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
    color: rgba(197, 60, 94, 1);
    line-height: 40px;
    text-align: center;
    background: white;
    border-right: 2px solid rgba(197, 60, 94, 1);
}
.index-search-input {
    height: 40px;
    width: 474px;
    border: none;
    box-shadow: none;
    text-align: center;
    font-size: 16px;
}
.index-search-input:focus {
    outline: none;
    border: none;
}
.index-top-bar-search-filter {
    height: 42px;
    width: 60px;
    line-height: 42px;
    text-align: center;
    color: rgba(197, 60, 94, 1);
    background: white;
    border-left: 2px solid rgba(197, 60, 94, 1);
    cursor: pointer;
    position: relative;
}
.index-top-bar-search-filter:hover .filter-select {
    display: flex;
}
.filter-title {
    height: 100%;
    width: 100%;
}
.filter-select {
    height: auto;
    width: 300px;
    position: absolute;
    top: 42px;
    left: 0;
    color: rgba(197, 60, 94, 1);
    background: white;
    box-shadow: 0 0 4px gray;
    border-radius: 3px;
    z-index: 21;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    font-size: 10px;
    box-sizing: border-box;
    padding: 10px;
    display: none;
}
.filter-select > .underline {
    height: 2px;
    width: 96%;
    background: rgba(197, 60, 94, 1);
}
.filter-institute,
.filter-classification {
    width: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-wrap: wrap;
}
.index-top-bar-search-button {
    height: 42px;
    width: 60px;
    line-height: 42px;
    text-align: center;
    color: rgba(197, 60, 94, 1);
    background: white;
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
    border-left: 2px solid rgba(197, 60, 94, 1);
}
.index-top-bar-button {
    height: 70px;
    width: 380px;
    display: flex;
    justify-content: flex-end;
    align-items: center;
}
.button  {
    height: 70px;
    width: 80px;
    line-height: 70px;
    text-align: center;
    position: relative;
    background: rgba(197, 60, 94, 1);
    color: white;
    transition: all .3s;
    cursor: pointer;
}
.index-top-bar-button > div:hover {
    background: white;
    color: rgba(197, 60, 94, 1);
}
.index-top-bar-head-container {
    height: 50px;
    width: 50px;
    border-radius: 50%;
    overflow: hidden;
    margin-right: 10px;
}
.index-top-bar-head-container > img {
    height: 100%;
    width: 100%;
}
#login,
#register {
    height: 300px;
    width: 400px;
    border-radius: 5px;
    background: rgba(197, 60, 94, 1);
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
    box-sizing: border-box;
    padding: 20px;
}
#register {
    height: 700px;
    justify-content: space-between;
}
.underline {
    height: 2px;
    width: 100%;
    margin: 5px;
    background: white;
}
select {
    appearance:none;
    -moz-appearance:none;
    -webkit-appearance:none;
    height: 40px;
    width: 260px;
    box-sizing: border-box;
    padding-left: 10px;
    border: none;
    border-radius: 5px;
    margin: 20px 0;
}
select:focus {
    outline: none;
    border: none;
}
.form-input {
    height: 40px;
    width: 260px;
    border-radius: 5px;
    box-shadow: none;
    border: none;
    text-align: center;
    margin: 5px 0 15px 0;
}
.close {
    height: 30px;
    width: 100%;
    line-height: 30px;
    text-align: right;
    color: white;
    box-sizing: border-box;
    padding-right: 20px;
}
.close-icon {
    cursor: pointer;
}
.form-input:focus {
    outline: none;
    border: none;
}
#loginSubmit {
    height: 40px;
    width: 180px;
    border-radius: 5px;
    margin: 10px 0;
    text-align: center;
    line-height: 40px;
    font-size: 12px;
    color: rgba(197, 60, 94, 1);
    background: white;
}
.warning {
    font-size: 10px;
    color: white;
}
.unread-container {
    height: 100%;
    width: 100%;
    line-height: 70px;
    text-align: center;
    position: relative;
}
.unread {
    height: 20px;
    width: 20px;
    border-radius: 50%;
    line-height: 20px;
    color: rgba(197, 60, 94, 1);
    background: white;
    position: absolute;
    top: 5px;
    right: 5px;
    display: inline;
}
</style>


