<template>
    <div class="file-upload-button" :style="customStyle" @click.stop="doInput">
        <i class="fa fa-plus fa-lg"></i>
    </div>
</template>

<script>
    export default {
        props: {
            customStyle: Object,
        },
        data() {
            return {
                
            }
        },
        methods: {
            doInput(){
                let _this = this;
                let inputObj = document.createElement('input');
                inputObj.addEventListener('change',function () {
                    let file = this.files[0];
                    if(!/image\/\w+/.test(file.type)){
                        alert("请确保文件为图像类型");
                        return false;
                    }
                    let reader = new FileReader();
                    reader.readAsDataURL(file);
                    reader.onload = function () {
                        _this.$emit("data", this.result);
                    }
                },false);
                inputObj.type = 'file';
                inputObj.accept = 'image/*';
                inputObj.click();
            },
        },
    }
</script>

<style scoped>
.file-upload-button {
    text-align: center;
    border-radius: 5px;
    margin: 10px 0;
    cursor: pointer;
}
</style>


