<template>
    <div class="container">
        <Header></Header>
        <div class="publish-main-container">
            <div class="publish-img-container">
                <div class="publish-large-img">
                    <img :src="imgData" v-if="imgData != '' && imgData != undefined">
                </div>
                <div class="publish-mini-img-container">
                    <Upload :customStyle="uploadStyle" @data="recvImg" v-if="imgArray.length != 4"></Upload>
                    <div class="publish-mini-img">
                        <i class="fa fa-close fa-lg" id="close" @click.stop="deleteImg(0)" v-if="imgArray[0]"></i>
                        <img :src="imgArray[0]" v-if="imgArray[0]"  @click.stop="showImg(0)">
                    </div>
                    <div class="publish-mini-img">
                        <i class="fa fa-close fa-lg" id="close" @click.stop="deleteImg(1)" v-if="imgArray[1]"></i>
                        <img :src="imgArray[1]" v-if="imgArray[1]" @click.stop="showImg(1)">
                    </div>
                    <div class="publish-mini-img">
                        <i class="fa fa-close fa-lg" id="close" @click.stop="deleteImg(2)" v-if="imgArray[2]"></i>
                        <img :src="imgArray[2]" v-if="imgArray[2]"  @click.stop="showImg(2)">
                    </div>
                    <div class="publish-mini-img" v-if="imgArray.length === 4">
                        <i class="fa fa-close fa-lg" id="close" @click.stop="deleteImg(3)" v-if="imgArray[3]"></i>
                        <img :src="imgArray[3]" v-if="imgArray[3]"  @click.stop="showImg(3)">
                    </div>
                </div>
            </div>
            <div class="publish-form-container">
                <select v-model="goodsClassification">
                    <option v-for="classification in classifications" :key="classification.id" :value="classification.id">{{ classification.name }}</option>
                </select>
                <input type="text" placeholder="商品标题" v-model="goodsTitle">
                <input type="text" placeholder="商品价格" v-model="goodsPrice">
                <input type="text" placeholder="联系方式" v-model="goodsContact">
                <textarea rows="8" placeholder="商品描述" v-model="goodsDesc"></textarea>
            </div>
            <div class="publish-button" @click="modifyGoods">修改</div>
        </div>
        <ScrollTop></ScrollTop>
        <Footer></Footer>
    </div>
</template>

<script>
import Vue from 'vue'
import Header from '../components/Header'
import ScrollTop from '../components/ScrollTop'
import Footer from '../components/Footer'
import Upload from '../components/Upload'
export default {
    components: {
        Header,
        ScrollTop,
        Footer,
        Upload,
    },
    data() {
        return {
            uploadStyle: {
                height: '80px',
                width: '80px',
                lineHeight: '80px',
                backgroundColor: 'rgba(197, 60, 94, 1)',
                color: 'white',
            },
            imgData: '',
            imgArray: [],
            goodsTitle: "",
            goodsPrice: "",
            goodsContact: "",
            goodsDesc: "",
            classifications: [],
            goodsClassification: "",
            goodsInfo: {},
        }
    },
    methods: {
        recvImg (data) {
            this.imgData = data;
            Vue.set(this.imgArray, this.imgArray.length, data);
        },
        deleteImg (id) {
            if(this.imgArray[id] != undefined && this.imgArray[id] != '') {
                Vue.delete(this.imgArray, id)
                if(this.imgArray[id] != undefined && this.imgArray[id] != '') {
                    this.imgData = this.imgArray[id];
                }
                else {
                    this.imgData = '';
                }
                
            }
        },
        showImg (id) {
            if(this.imgArray[id] != undefined && this.imgArray[id] != '') {
                this.imgData = this.imgArray[id];
            }
        },
        modifyGoods () {
            const url = '/commodity/modify'
            const data = {
                commodityId: this.$route.query.id,
                title: this.goodsTitle,
                contact: this.goodsContact,
                description: this.goodsDesc,
                classification: this.goodsClassification,
                images: this.imgArray.join("@"),
                price: this.goodsPrice,
                modifier: this.getCookie('userid'),
            };
            this.myAjax_post(url, data).then(response => {
                alert(response.msg);
                if(response.flag == 1) {
                    this.$router.push({ path: 'goods', query: { id: this.$route.query.id, pageStart: 0}});
                }
            });
        },
        getClassifications () {
            this.myAjax_get('/index/classification').then(response =>{
                if(response.flag == 1) {
                    this.classifications = [];
                    for(let i in response.data) {
                        this.classifications.push(response.data[i]);
                    }
                }
            });
        },
        getGoodsInfo () {
            const url = '/commodity/detail';
            const data = {
                commodityId: this.$route.query.id,
                pageSize: this.constant.detailPageSize,
            };
            this.myAjax_post(url, data).then(response => {
                if(response.flag == 1) {
                    if(response.data.publisherId == this.getCookie('userid')) {
                        this.goodsInfo = response.data;
                        this.imgArray = this.goodsInfo.images.split('@');
                        this.imgData = response.data.images.split('@')[0];
                        this.goodsTitle = response.data.title;
                        this.goodsPrice = response.data.price;
                        this.goodsContact = response.data.contact;
                        this.goodsDesc = response.data.description;
                        this.goodsClassification = response.data.classificationId;
                    }
                    else {
                        alert('只有卖家可以修改自己的商品');
                        this.$router.push({path: '/goods', query: {id: this.$route.query.id}});
                    }
                }
            });
        },
    },
    created () {
        this.getClassifications();
        this.getGoodsInfo();
    }
}
</script>

<style scoped>
.container {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
}
.publish-main-container {
    min-height: 100%;
    height: auto;
    width: 1000px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    box-sizing: border-box;
    padding: 100px 100px 20px 100px;
    margin: 40px 0;
}
.publish-form-container {
    height: auto;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
}
.publish-form-container > input,
.publish-form-container > select {
    height: 40px;
    width: 300px;
    border-radius: 5px;
    border: 2px solid rgba(197, 60, 94, 1);
    color: black;
    margin: 10px;
    box-sizing: border-box;
    padding: 0 10px;
}
.publish-form-container > textarea {
    height: 200px;
    width: 300px;
    border-radius: 5px;
    border: 2px solid rgba(197, 60, 94, 1);
    color: black;
    margin: 10px;
    box-sizing: border-box;
    padding: 10px;
}
.publish-form-container > input:focus,
.publish-form-container > select:focus,
.publish-form-container > textarea:focus {
    outline: none;
    border: 2px solid rgba(197, 60, 94, 1);
}
.publish-img-container {
    height: 340px;
    width: 400px;
    box-sizing: border-box;
    padding: 10px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}
.publish-large-img {
    height: 200px;
    width: 200px;
    background: rgba(197, 60, 94, 1);
    border-radius: 5px;
    overflow: hidden;
}
.publish-large-img > img {
    height: 100%;
    width: 100%;
}
.publish-mini-img-container {
    height: 120px;
    width: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
}
.publish-mini-img,
.publish-img-addbutton {
    height: 80px;
    width: 80px;
    background: rgba(197, 60, 94, 1);
    border-radius: 5px;
    overflow: hidden;
    position: relative;
}
#close {
    position: absolute;
    top: 5px;
    right: 5px;
    height: 20px;
    width: 20px;
    line-height: 20px;
    text-align: center;
    border-radius: 50%;
    color: rgba(197, 60, 94, 1);
    background: white;
    display: none;
    transition: all 0.5s;
    cursor: pointer;
}
.publish-mini-img > img {
    height: 100%;
    width: 100%;
    cursor: pointer;
}
.publish-mini-img:hover #close {
    display: inline;
}
.publish-img-addbutton {
    line-height: 80px;
    text-align: center;
    color: white;
    font-size: 30px;
    font-weight: lighter;
}
.publish-button {
    height: 30px;
    width: 100px;
    line-height: 30px;
    border-radius: 5px;
    text-align: center;
    background: rgba(197, 60, 94, 1);
    color: white;
    margin: 20px 0;
    cursor: pointer;
}
</style>


