<template>
    <div class="container">
        <Header></Header>
        <div class="admin-main-container">
            <TagManage></TagManage>
            <InstituteManage></InstituteManage>
            <UserManage></UserManage>
            <BoardManage></BoardManage>
            <BoardEditor></BoardEditor>
        </div>
        <ScrollTop></ScrollTop>
        <Footer></Footer>
    </div>
</template>

<script>
import Header from '../components/Header'
import TagManage from '../components/TagManage'
import InstituteManage from '../components/InstituteManage'
import UserManage from '../components/UserManage'
import BoardManage from '../components/BoardManage'
import BoardEditor from '../components/BoardEditor'
import ScrollTop from '../components/ScrollTop'
import Footer from '../components/Footer'
export default {
    components: {
        Header,
        TagManage,
        InstituteManage,
        UserManage,
        BoardManage,
        BoardEditor,
        ScrollTop,
        Footer,
    },
    created () {
        if(this.getCookie('admin') != 1) {
            this.$router.push({path: '/index'});
        }
    }
}
</script>

<style scoped>
.container {
    width: 100%;
    height: 100%;
}
.admin-main-container {
    width: 100%;
    height: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
}
</style>


