<template>
<div class="user-manage-container">
    <div class="user-manage-title">用户管理</div>
    <div class="user-manage">
        <div class="add-user-warning" v-if="warningShow">{{warningText}}</div>
        <div class="user-manage-add-group">
            <input type="text" placeholder="查询用户" id="add-user-content" v-model="searchUserName" @keyup.enter="searchUser">
            <div id="add-user-button" @click="searchUser">查找</div>
        </div>
        <div class="user-manage-list">
            <div class="user-manage-item" v-for="item in searchUserList" :key="item.id">
                <div class="user-manage-item-info">
                    <div class="user-manage-item-head">
                        <img :src="item.avatar" v-if="item.avatar != ''">
                    </div>
                    <div class="user-manage-item-title">
                        {{item.username}}
                    </div>
                </div>
                <div class="user-manage-item-button">
                    <div @click="deleteUser(item.id)">删除</div>
                </div>
            </div>
        </div>
    </div>
</div>
</template>

<script>
export default {
    data() {
        return {
            searchUserName: "",
            searchUserList: [],
            warningShow: false,
            warningText: "",
        }
    },
    methods: {
        searchUser () {
            const url = '/user/admin/searchUser';
            const data = {
                search: this.searchUserName,
            };
            this.myAjax_post(url, data).then(response => {
                if(response.flag == 1) {
                    this.searchUserList = [];
                    for(let i in response.data) {
                        this.searchUserList.push(response.data[i]);
                    }
                }
                else {
                    this.searchUserList = [];
                }
            });
        },
        deleteUser (id) {
            const url = '/user/admin/deleteUser';
            const data = {
                userId: id
            };
            this.myAjax_post(url, data).then(response => {
                if(response.flag == 1) {
                    alert(response.msg);
                    this.searchUser();
                }
            });
        }
    }
}
</script>

<style scoped>
.user-manage-container {
    height: 500px;
    width: 380px;
    /* background: rgba(197, 60, 94, 1); */
    border: 2px solid rgba(197, 60, 94, 1);
    margin: 100px 20px 20px 20px;
    /* border-radius: 5px; */
}
.user-manage-title {
    height: 30px;
    width: 100%;
    line-height: 30px;
    box-sizing: border-box;
    padding: 0 20px;
    font-size: 18px;
    color: rgba(197, 60, 94, 1);
    margin: 20px 0 10px 0;
}
.add-user-warning {
    font-size: 12px;
    color: rgb(248, 50, 50);
    margin: 0 20px;
}
.user-manage-add-group {
    height: 50px;
    width: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}
#add-user-content {
    height: 30px;
    width: 200px;
    /* border: none; */
    box-shadow: none;
    text-align: left;
    margin: 0 20px;
    border: 1px solid gray;
}
#add-user-content:focus {
    outline: none;
    border: none;
    border: 1px solid gray;
}
#add-user-button {
    height: 30px;
    width: 50px;
    line-height: 30px;
    text-align: center;
    background: rgba(197, 60, 94, 1);
    color: white;
    /* border-radius: 5px; */
    font-size: 12px;
    cursor: pointer;
}
.user-manage-list {
    height: 360px;
    width: 100%;
    box-sizing: border-box;
    padding: 10px;
    margin: 10px 0;
    overflow: auto;
}
.user-manage-item {
    height: 40px;
    width: 100%;
    box-sizing: border-box;
    padding: 0 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.user-manage-item:nth-child(2n) {
    background: rgb(194, 192, 192);
}
.user-manage-item-head {
    height: 40px;
    width: 40px;
    border-radius: 50%;
    background: rgba(197, 60, 94, 1);
    overflow: hidden;
}
.user-manage-item-head > img {
    height: 100%;
    width: 100%;
}
.user-manage-item-info {
    display: flex;
    justify-content: center;
    align-items: center;
}
.user-manage-item-title {
    margin:  0 10px;
}
.user-manage-item-button {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 12px;
}
.user-manage-item-button > div {
    margin: 0 10px;
    cursor: pointer;
}
</style>


