<template>
    <div class="index-scroll-top" @click="scrollTop">
            <i class="fa fa-chevron-up fa-lg"></i>
    </div>
</template>

<script>
export default {
    methods: {
        scrollTop() {
            window.scrollTo(0, 0);
        },
    },
}
</script>

<style scoped>
.index-scroll-top {
    position: fixed;
    bottom: 50px;
    right: 40px;
    height: 50px;
    width: 50px;
    border-radius: 5px;
    background: rgba(197, 60, 94, 1);
    line-height: 50px;
    text-align: center;
    color: white;
    transition: all .5s;
    cursor: pointer;
}
.index-scroll-top:hover {
    color: rgba(197, 60, 94, 1);
    background: white;
}
</style>


