<template>
    <div class="board-pagination">
        <div class="board-pagination-button"  @click.stop="changeNum(parseInt(data.currentIndex)-1)" v-if="data.currentIndex > 1">上一页</div>
        <div class="board-pagination-num-group">
            <div v-if="data.pages < 7" 
            v-for="page in data.pages"
            :key="page"
            :class="['board-pagination-num', page == data.currentIndex ? 'select' : '']"
            @click.stop="changeNum(page)">
            {{page}}
            </div>

            <div
            v-if="data.pages >= 7 && data.currentIndex < 7 - 2"
            v-for="page in 7"
            :key="page" :class="['board-pagination-num', page == data.currentIndex ? 'select' : '']" @click.stop="changeNum(page)">
                {{page}}
            </div>

            <div
            v-if="data.pages >= 7 && data.currentIndex >= 7 - 2 && data.currentIndex < data.pages - 3"
            v-for="page in [data.currentIndex-3,data.currentIndex-2,data.currentIndex-1,data.currentIndex,data.currentIndex+1,data.currentIndex+2,data.currentIndex+3]"
            :key="page" :class="['board-pagination-num', page == data.currentIndex ? 'select' : '']"
            @click.stop="changeNum(page)">
                {{page}}
            </div>

            <div
            v-if="data.pages >= 7 && data.currentIndex >= 7 - 2 && data.currentIndex >= data.pages - 3"
            v-for="page in [data.pages-6,data.pages-5,data.pages-4,data.pages-3,data.pages-2,data.pages-1,data.pages]"
            :key="page" :class="['board-pagination-num', page == data.currentIndex ? 'select' : '']"
            @click.stop="changeNum(page)">
                {{page}}
            </div>
        </div>
        <div class="board-pagination-button" @click.stop="changeNum(parseInt(data.currentIndex)+1)" v-if="data.currentIndex < data.pages">下一页</div>
    </div>
</template>

<script>
    export default {
        props: {
            data: Object,
        },
        methods: {
            changeNum: function(id) {
                if(id != this.data.currentIndex) {
                    this.$emit("numberChange", id);
                }
            }
        },
    }
</script>

<style scoped>
.board-pagination {
    width: 100%;
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    margin: 40px;
}
.board-pagination-button {
    height: 40px;
    line-height: 40px;
    text-align: center;
    width: 80px;
    background: rgba(197, 60, 94, 1);
    cursor: pointer;
}
.board-pagination-num {
    height: 40px;
    line-height: 40px;
    text-align: center;
    width: 40px;
    margin: 0 5px;
    background: rgba(197, 60, 94, 1);
    cursor: pointer;
}
.board-pagination-num.select {
    color: rgba(197, 60, 94, 1);
    background: white;
    box-sizing: border-box;
    border: 1px solid rgba(197, 60, 94, 1);
    cursor: not-allowed;
}
.board-pagination-num-group {
    display: flex;
    justify-content: center;
    align-items: center;
}
</style>


