<template>
    <div class="goods-item" @click="linkTo(data.id)">
        <div class="goods-main-container">
            <div class="goods-img">
                <img :src="data.images.split('@')[0]">
            </div>
            <div class="goods-text">
                <div class="goods-title">
                    <div>{{data.title}}</div>
                    <div class="goods-price">¥{{data.price}}</div>
                </div>
                <div class="goods-info">{{data.description}}</div>
                <div class="goods-time">{{new Date(data.time*1000).toLocaleString()}}</div>
                <div class="good-statistics">
                    <div>浏览数({{data.browse}})</div>
                    <div>留言数({{data.reply}})</div>
                </div>
            </div>
        </div>
        <!-- <div class="goods-button-container">
            <div class="goods-button-link">查看商品</div>
            <div class="goods-button-close">关闭状态</div>
            <div class="goods-button-delete">删除商品</div>
        </div> -->
    </div>
</template>

<script>
    export default {
        props: ['data'],
        data () {
            return {
            }
        },
        methods: {
            linkTo: function(id) {
                this.$router.push({
                    path: '/goods',
                    query: {
                        id: id,
                        pageStart: 0,
                    }
                })
            },
        }
    }
</script>

<style scoped>
.goods-item {
    height: 200px;
    width: 100%;
    margin: 10px 0;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    cursor: pointer;
}
.goods-main-container {
    height: 100%;
    flex-grow: 1;
    /* background: rgba(197, 60, 94, 1); */
    box-sizing: border-box;
    padding: 20px;
    border-radius: 5px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    color: black;
    transition: all .5s ease-in-out;
}
.goods-img {
    height: 160px;
    width: 160px;
    border-radius: 5px;
    overflow: hidden;
    background: rgba(197, 60, 94, 1);
}
img {
    height: 100%;
    width: 100%;
}
.goods-text {
    flex-grow: 1;
    height: 160px;
    margin: 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
    position: relative;
    z-index: 10;
}
.goods-price {
    font-size: 28px;
    margin-top: 10px;
    color: rgba(197, 60, 94, 1);
}
.goods-price::first-letter {
    margin: 0 8px;
}
.good-statistics {
    position: absolute;
    bottom: 0;
    right: 0;
    height: 30px;
    width: 180px;
    line-height: 30px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.good-statistics > div {
    font-size: 10px;
    line-height: 30px;
}
.goods-time {
    font-size: 12px;
}
.goods-title {
    height: 60px;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
}
.goods-info {
    flex-grow: 1;
    width: 100%;
    box-sizing: border-box;
    padding: 15px 0;
    font-size: 14px;
}
.goods-button-container {
    height: 100%;
    width: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    overflow: hidden;
}
.goods-button-container > div {
    height: 50px;
    width: 100px;
    line-height: 50px;
    text-align: center;
    color: black;
}
</style>


