<template>
    <div class="index-container">
        <Header></Header>
        <div class="index-main-container">
            <div class="index-main-top-item">
                <div class="index-main-top-item-menu">
                    <router-link v-for="menu in classifications" :key="menu.id" :to="{path: '/board', query: {id: menu.id, pageStart: 0,}}" tag="div"  class="index-main-top-item-menu-list">
                         {{menu.name}}
                    </router-link>
                </div>
                <div class="index-main-top-item-banner">
                    <img src="https://timgsa.baidu.com/timg?image&quality=80&size=b10000_10000&sec=1524286383&di=6f4ed9215d6e48fddb190a61285e4a41&src=http%3A%2F%2Fpic.58pic.com%2F58pic%2F16%2F52%2F74%2F61R58PIC4jZ_1024.jpg">
                </div>
                <div class="index-main-top-item-news">
                    <div class="index-main-top-item-news-list">公告板</div>
                    <div class="index-main-top-item-news-list" v-for="notice in notices" :key="notice.id" @click="linkToNotice(notice.id)">
                        {{notice.title}}
                    </div>
                </div>
            </div>
            <div class="index-main-good-item" v-for="item in classifications" :key="item.id">
                <div class="index-main-item-title">
                    <router-link :to="{path: '/board', query: {id: item.id , pageStart: 0}}" tag="div">{{item.name}}</router-link>
                </div>
                <div class="index-main-item-goods-container">
                    <router-link class="index-main-item-goods" tag="div" :to="{path: '/goods', query: {id: item.commodityIds[index], pageStart: 0,}}" v-for="(goods, index) in item.images" :key="item.commodityIds[index]">
                        <img :src="goods" v-if="goods != ''">
                    </router-link>
                </div>
            </div>
        </div>
        <Footer></Footer>
        <ScrollTop></ScrollTop>
    </div>
</template>

<script>
import Vue from 'vue'
import Header from '../components/Header'
import ScrollTop from '../components/ScrollTop'
import Footer from '../components/Footer'
export default {
    components: {
        Header,
        ScrollTop,
        Footer,
    },
    data() {
        return {
            notices: [{
                id: "",
                
            },{
                id: "",
                
            },{
                id: "",
                
            },{
                id: "",
                
            },{
                id: "",
                
            },{
                id: "",
                
            }],
            classifications: []
        }
    },
    methods: {
        getNotices () {
            this.myAjax_get('/index/sixNoticeTitle').then(response =>{
                if(response.flag == 1) {
                    this.notices = [{id: "",},{id: "",},{id: "",},{id: "",},{id: "",},{id: "",}];
                    for(let i = 0;i < response.data.length; i++) {
                        Vue.set(this.notices, i, response.data[i]);
                    }
                    console.log(this.notices);
                }
            });
        },
        getClassification () {
            this.myAjax_get('/index/classification').then(response =>{
                if(response.flag == 1) {
                    for(let i in response.data) {
                        this.classifications.push(response.data[i]);
                    }
                }
            });
        },
        linkToNotice:  function(id) {
            this.$router.push({path: '/notice', query: {id: id}})
        }
    },
    created () {
        this.getNotices();
        this.getClassification();
    }
}
</script>

<style scoped>
.index-container {
    min-height: 100%;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: column;
}
.index-main-container {
    border-radius: 5px;
    height: auto;
    width: 100%;
    background: white;
    box-sizing: border-box;
    padding: 80px 30px 20px 30px;
}
.index-main-top-item,
.index-main-good-item {
    min-width: 1380px;
    height: 400px;
    background: white;
    border-radius: 5px;
    margin: 20px 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.index-main-top-item-menu {
    height: 380px;
    width: 320px;
    border-radius: 5px;
    background: white;
    margin: 10px;
    box-sizing: border-box;
    padding: 20px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
}
.index-main-top-item-menu-list {
    color: white;
    width: 100%;
    height: 80px;
    border-radius: 5px;
    box-sizing: border-box;
    padding-left: 10px;
    background: rgba(197, 60, 94, 1);
    margin: 5px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    font-size: 16px;
    cursor: pointer;
}
.list-tag {
    color: rgb(226, 220, 220);
    margin: 0 5px;
    font-size: 12px;
}
.index-main-top-item-banner {
    height: 380px;
    width: 600px;
    border-radius: 5px;
    margin: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
}
.index-main-top-item-banner > img {
    height: auto;
    width: 100%;
}
.index-main-top-item-news {
    height: 380px;
    width: 400px;
    border-radius: 5px;
    background: white;
    margin: 10px;
    box-sizing: border-box;
    padding: 20px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-wrap: wrap;
}
.index-main-top-item-news-list:first-of-type {
    height: 80px;
    line-height: 80px;
    color: white;
    text-align: center;
    font-size: 24px;
}
.index-main-top-item-news-list {
    width: 100%;
    height: 40px;
    margin: 5px;
    border-radius: 5px;
    background: rgba(197, 60, 94, 1);
    color: white;
    line-height: 40px;
    box-sizing: border-box;
    padding-left: 15px;
    cursor: pointer;
    display: block;
}
.index-main-good-item {
    flex-direction: column;
    justify-content: space-between;
}
.index-main-item-title {
    color: rgba(197, 60, 94, 1);;
    font-size: 40px;
    height: 60px;
    line-height: 60px;
    width: 95%;
    border-bottom: 2px solid rgba(197, 60, 94, 1);;
    margin: 30px 0 20px 0;
}
.index-main-item-title > div {
    height: 100%;
    width: 200px;
    text-align: left;
    cursor: pointer;
}
.index-main-item-goods-container {
    height: 300px;
    width: 100%;
    box-sizing: border-box;
    padding: 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.index-main-item-goods {
    height: 240px;
    width: 240px;
    border-radius: 5px;
    cursor: pointer;
}
img {
    height: 100%;
    width: 100%;
}
</style>


