<template>
    <div class="container">
        <Header></Header>
        <div class="board-main-body">
            <div class="bread-nav"><router-link to="/">闲猫</router-link> / {{classificationsName}}</div>
            <div class="filter-bar">
                <div class="filter-bar-item">
                    时间排序
                    <div class="angle-group">
                        <i class="fa fa-caret-up fa-lg angle-active"></i>
                        <i class="fa fa-caret-down fa-lg"></i>
                    </div>
                </div>
                <div class="filter-bar-item">
                    价格排序
                    <div class="angle-group">
                        <i class="fa fa-caret-up fa-lg angle-active"></i>
                        <i class="fa fa-caret-down fa-lg"></i>
                    </div>
                </div>
                <div class="filter-bar-item">
                    信用排序
                    <div class="angle-group">
                        <i class="fa fa-caret-up fa-lg angle-active"></i>
                        <i class="fa fa-caret-down fa-lg"></i>
                    </div>
                </div>
                <div class="filter-bar-item">
                    成色排序
                    <div class="angle-group">
                        <i class="fa fa-caret-up fa-lg angle-active"></i>
                        <i class="fa fa-caret-down fa-lg"></i>
                    </div>
                </div>
             </div>
             <div class="goods-container">
                 <div v-if="noneGoodsWarning" class="warning">该分类下暂无商品</div>
                 <Goods :data="goods" v-for="goods in boardGoods" :key="goods.id"></Goods>
             </div>
            <Pagination :data="pagination" @numberChange="changeNum"></Pagination>
        </div>
        <Footer></Footer>
        <ScrollTop></ScrollTop>
    </div>
</template>

<script>
import Header from '../components/Header'
import ScrollTop from '../components/ScrollTop'
import Pagination from '../components/Pagination'
import Footer from '../components/Footer'
import Goods from '../components/Goods'
export default {
    components: {
        Header,
        ScrollTop,
        Footer,
        Pagination,
        Goods,
    },
    data () {
        return {
            boardGoods: [],
            noneGoodsWarning: false,
            pagination: {
                pages: 0,
                currentIndex: parseInt(this.$route.query.pageStart/this.constant.boardPageSize)+1,
            },
            classificationsName: "",
        }
    },
    watch: {
        '$route' (to, from) {
            this.$router.go(0)
        }
    },
    methods: {
        changeNum (id) {
            this.$router.push({path: '/board', query: {
                id: this.$route.query.id,
                pageStart: (parseInt(id)-1)*this.constant.boardPageSize,
            }});
        },
        getClassification () {
            this.myAjax_get('/index/classification').then(response =>{
                if(response.flag == 1) {
                    for(let i in response.data) {
                        if(response.data[i].id == this.$route.query.id) {
                            this.classificationsName = response.data[i].name
                        }
                    }
                }
            });
        },
        getBoardPageNum () {
            const url = '/commodity/classPages';
            const data = {
                classificationId: this.$route.query.id,
                pageSize: this.constant.boardPageSize,
            };
            this.myAjax_post(url, data).then(response => {
                if(response.flag == 1){
                    this.pagination.pages = response.data.pages;
                }
            });
        },
        getBoardGoods () {
            const url = '/commodity/class';
            const data = {
                classificationId: this.$route.query.id,
                start: this.$route.query.pageStart,
                pageSize: this.constant.boardPageSize,
            };
            this.myAjax_post(url, data).then(response =>{
                if(response.flag == 1) {
                    this.boardGoods = [];
                    for (let i in response.data) {
                        this.boardGoods.push(response.data[i]);
                    }
                    this.noneGoodsWarning = (this.boardGoods.length == 0 ? true : false);
                }
            });
        }
    },
    created () {
        this.getBoardPageNum();
        this.getBoardGoods();
        this.getClassification();
    }
}
</script>

<style scoped>
.warning {
    height: 240px;
    width: 100%;
    line-height: 240px;
    text-align: center;
    font-size: 24px;
    color: rgba(197, 60, 94, 1);
}
.container {
    height: 100%;
    width: 100%;
    margin: 0;
    padding: 0;
}
.board-main-body {
    height: auto;
    width: 100%;
    box-sizing: border-box;
    padding: 100px 100px 20px 100px;
}
.bread-nav {
    color: rgba(197, 60, 94, 1);
}
.bread-nav > a,
.bread-nav > a:link,
.bread-nav > a:visited,
.bread-nav > a:hover,
.bread-nav > a:active {
    color: rgba(197, 60, 94, 1);
    text-decoration: none;
}
.filter-bar {
    height: 50px;
    width: 100%;
    display: flex;
    justify-content: flex-end;
    align-items: center;
}
.filter-bar-item {
    height: 30px;
    width: 100px;
    background: #FFEFE5;
    border-radius: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: rgba(197, 60, 94, 1);
    cursor: pointer;
    margin: 0 10px;
}
.angle-group {
    font-size: 10px;
    margin-left: 5px;
    height: 30px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    color: gray;
}
.angle-active {
    color: rgba(197, 60, 94, 1) !important;
}
.goods-container {
    height: auto;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
}
</style>


