<template>
    <div class="container">
        <Header></Header>
        <div class="notice-container">
            <div class="notice-title">
                {{noticeTitle}}
            </div>
            <div class="underline"></div>
            <div>{{new Date(noticeTime*1000).toLocaleString()}}</div>
            <div class="notice-text">
                {{noticeText}}
            </div>
        </div>
        <ScrollTop></ScrollTop>
        <Footer></Footer>
    </div>
</template>

<script>
import Header from '../components/Header'
import ScrollTop from '../components/ScrollTop'
import Footer from '../components/Footer'
export default {
    components: {
        Header,
        ScrollTop,
        Footer
    },
    data() {
        return {
            noticeTitle: "",
            noticeText: "",
            noticeTime: 0,
        }
    },
    methods: {
        getNotice () {
            const url = '/index/notice';
            const data = {
                id: this.$route.query.id,
            };
            this.myAjax_post(url, data).then(response => {
                if(response.flag == 1) {
                    this.noticeText = response.data.text;
                    this.noticeTitle = response.data.title;
                    this.noticeTime = response.data.publishTime;
                }
            });
        }
    },
    created () {
        this.getNotice();
    }
}
</script>

<style scoped>
.container {
    height: 100%;
    width: 100%;
}
.notice-container {
    height: 100%;
    width: 100%;
    box-sizing: border-box;
    padding: 100px;
}
.notice-title {
    font-size: 30px;
    color: rgba(197, 60, 94, 1);
}
.underline {
    height: 2px;
    width: 96%;
    background: rgba(197, 60, 94, 1);
    margin: 10px 0;
}
.notice-text {
    width: 100%;
    box-sizing: border-box;
    padding: 50px 200px;
}
</style>


