import Vue from 'vue';
import Router from 'vue-router';
import index from '@/pages/index';
import board from '@/pages/board';
import goods from '@/pages/goods';
import mine from '@/pages/mine';
import publish from '@/pages/publish';
import message from '@/pages/message';
import admin from '@/pages/admin';
import modifyinfo from '@/pages/modifyinfo';
import modifypassword from '@/pages/modifypassword';
import modifypublish from '@/pages/modifypublish';
import notice from '@/pages/notice';
import search from '@/pages/search';

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/index',
      meta: {
        title: ' ',
      },
    }, {
      path: '/index',
      name: 'index',
      component: index,
      meta: {
        title: '闲猫——我想不到应该用的什么四楼根',
      },
    }, {
      path: '/board',
      name: 'board',
      component: board,
      meta: {
        title: '板块区——我想不到应该用的什么四楼根',
      },
    }, {
      path: '/goods',
      name: 'goods',
      component: goods,
      meta: {
        title: '商品详情——我想不到应该用的什么四楼根',
      },
    }, {
      path: '/mine',
      name: 'mine',
      component: mine,
      meta: {
        title: '个人中心——我想不到应该用的什么四楼根',
      },
    }, {
      path: '/publish',
      name: 'publish',
      component: publish,
      meta: {
        title: '发布页面——我想不到应该用的什么四楼根',
      },
    }, {
      path: '/message',
      name: 'message',
      component: message,
      meta: {
        title: '消息页面——我想不到应该用的什么四楼根',
      },
    }, {
      path: '/admin',
      name: 'admin',
      component: admin,
      meta: {
        title: '管理界面——我想不到应该用的什么四楼根',
      },
    }, {
      path: '/modifyinfo',
      name: 'modifyinfo',
      component: modifyinfo,
      meta: {
        title: '修改信息——我想不到应该用的什么四楼根',
      },
    }, {
      path: '/modifypassword',
      name: 'modifypassword',
      component: modifypassword,
      meta: {
        title: '修改密码——我想不到应该用的什么四楼根',
      },
    }, {
      path: '/modifypublish',
      name: 'modifypublish',
      component: modifypublish,
      meta: {
        title: '修改商品——我想不到应该用的什么四楼根',
      },
    }, {
      path: '/notice',
      name: 'notice',
      component: notice,
      meta: {
        title: '公告详情——我想不到应该用的什么四楼根',
      },
    }, {
      path: '/search',
      name: 'search',
      component: search,
      meta: {
        title: '搜索结果——我想不到应该用的什么四楼根',
      },
    }],
});



