<template>
    <div class="container">
        <Header></Header>
        <div class="modify-info-container">
            <div class="modify-head-container">
                <img :src="headBase64">
                <div class="modify-head-button">
                    <Upload :customStyle="uploadButton" @data="changeHead"></Upload>
                </div>
            </div>
            <label>UID</label>
            <input type="text" :placeholder="userId" class="input-item" disabled style="cursor: not-allowed">
            <div class="input-group">
                <label for="username">用户名</label>
                <input type="text" placeholder="用户名" v-model="userName" class="input-item" id="username"/>

                <label for="name">姓名</label>
                <input type="text" placeholder="姓名" v-model="name" class="input-item" id="name"/>

                <label for="phone-number">手机号</label>
                <input type="text" placeholder="手机号" v-model="phoneNumber" class="input-item" id="phone-number"/>

                <label for="stu-id">学号</label>
                <input type="text" placeholder="学号" v-model="stuId" class="input-item" id="stu-id"/>
            </div>
            <select v-model="institute"  class="input-item">
                <option v-for="(value, key) in instituteArray" :key="key" :value="key">{{value}}</option>
            </select>

            <div class="save-modify-button" @click="update">保存</div>
        </div>
        <ScrollTop></ScrollTop>
        <Footer></Footer>
    </div>
</template>

<script>
import Header from '../components/Header'
import Upload from '../components/Upload'
import ScrollTop from '../components/ScrollTop'
import Footer from '../components/Footer'
export default {
    components: {
        Header,
        Upload,
        ScrollTop,
        Footer
    },
    data () {
        return {
            uploadButton: {
                height: '80px',
                lineHeight: '80px',
                width: '200px',
                backgroundColor: 'white',
                color: 'black',
                opacity: '0.7',
                margin: '0',
            },
            userId: "",
            userName: "",
            name: "",
            phoneNumber: "",
            stuId: "",
            institute: "",
            instituteArray: [],
            headBase64: "",
        }
    },
    methods: {
        changeHead: function(data) {
            this.headBase64 = data;
        },
        getUserInfo () {
            const url = '/user/detail/';
            const data = {
                userId: this.getCookie('userid'),
            };
            this.myAjax_post(url, data).then(response => {
                if(response.flag == 1){
                    this.userName = response.data.username;
                    this.name = response.data.name;
                    this.phoneNumber = response.data.phone;
                    this.stuId = response.data.stuID;
                    this.userId = response.data.id;
                    this.institute = response.data.instituteId;
                    this.headBase64 = response.data.avatar;
                }
            });
        },
        getInistitute () {
            const url = '/index/institute';
            this.myAjax_get(url).then(response => {
                if(response.flag == 1){
                    this.instituteArray = response.data;
                }
            });
        },
        update () {
            const url = '/user/update';
            const data = {
                userId: this.getCookie('userid'),
                userName: this.userName,
                name: this.name,
                phone: this.phoneNumber,
                instituteId: this.institute,
                stuId: this.stuId,
                email: 'example@example.com',
                avatar: this.headBase64,
            };
            this.myAjax_post(url, data).then(response => {
                if(response.flag == 1){
                    alert(response.msg);
                    localStorage.setItem('avatar', data.avatar);
                    this.$router.push({path: '/mine'});
                }
            });
        }
    },
    created () {
        if(!this.checkCookie('login')) {
            this.$router.push({path: '/index'});
        }
        else {
            this.getUserInfo();
            this.getInistitute();
        }
    }

}
</script>

<style scoped>
.container {
    height: 100%;
    width: 100%;
}
.modify-info-container {
    min-height: 100%;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin: 20px 0;
    box-sizing: border-box;
    padding: 100px;
}
.modify-head-container {
    height: 200px;
    width: 200px;
    border-radius: 50%;
    overflow: hidden;
    position: relative;
}
.modify-head-container > img {
    height: 100%;
    width: 100%;
}
.modify-head-button {
    position: absolute;
    bottom: 0;
    left: 0;
}
.input-item {
    height: 40px;
    width: 240px;
    margin: 20px 0;
    text-align: center;
}
.save-modify-button {
    height: 30px;
    width: 60px;
    border-radius: 5px;
    background: rgba(197, 60, 94, 1);
    color: white;
    line-height: 30px;
    text-align: center;
    margin: 40px 0;
    cursor: pointer;
}

.input-group {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
}
input:focus {
    outline: none;
}
label {
    margin-top: 20px;
    font-size: 12px;
    color: rgba(197, 60, 94, 1);
}
</style>


