<template>
    <div class="container">
        <Header></Header>
        <div class="goods-main-body">
            <div class="bread-nav"><router-link to="/">闲猫</router-link> / <router-link :to="{path: '/board', query: {id: goodsInfo.classificationId, pageStart: 0}}">{{goodsInfo.classificationName}}</router-link> / {{goodsInfo.title}}</div>
            <div class="goods-items-container">
                <div class="goods-item">
                    <div class="goods-item-user">
                        <div class="goods-user-img">
                            <img :src="goodsInfo.publisherAvatar" >
                        </div>
                        <div class="goods-user-text">
                            <div class="goods-user-id">{{goodsInfo.publisherName}}</div>
                            <div v-if="goodsInfo.publisherIsAdmin == 1">管理员</div>
                            <div v-if="goodsInfo.publisherIsAdmin != 1">普通用户</div>
                            <div class="send-message-button" v-if="userId != goodsInfo.publisherId">
                                <div @click="sendMessageShow(goodsInfo.publisherName, goodsInfo.publisherId)">
                                    <i class="fa fa-commenting fa-lg"></i>发私信
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="goods-item-text-container">
                        <div class="goods-item-top-bar">
                            <div class="goods-item-floor-num">#1</div>
                            <div>发布于{{new Date(goodsInfo.time*1000).toLocaleString()}}</div>
                            <div class="goods-item-button-group">
                                <div class="goods-item-delete" v-if="goodsInfo.publisherId == userId" @click="modifyGoods">编辑</div>
                                <div class="goods-item-delete" v-if="isAdmin || goodsInfo.publisherId == userId" @click="deleteGoods">删除</div>
                            </div>
                        </div>
                        <div class="goods-item-main-body">
                            <div class="goods-item-banner">
                                <div class="goods-large-img">
                                    <img :src="goodsLargeImg" v-if='goodsLargeImg != "" '>
                                </div>
                                <div class="goods-mini-imgs">
                                    <div v-for="item in goodsInfo.images" :key="item">
                                        <img :src=item @click="showLargeImg(item)">
                                    </div>
                                </div>
                            </div>
                            <div class="goods-item-info">
                                <div class="goods-item-title">{{goodsInfo.title}}</div>
                                <div class="goods-item-price">¥ {{goodsInfo.price}}</div>
                                <div class="goods-item-text">
                                    {{goodsInfo.description}}
                                </div>
                                <div class="goods-item-bottom-bar">
                                    <div class="goods-item-contact-group">
                                        <div class="goods-item-phone">
                                            联系方式: {{goodsInfo.contact}}
                                        </div>
                                        <!-- <div class="goods-item-qq"><i class="fa fa-qq fa-lg"></i> 123456789</div>
                                        <div class="goods-item-wechat"><i class="fa fa-wechat fa-lg"></i> 12345678909</div> -->
                                    </div>
                                    <div class="goods-item-status" @click="buy" v-if="goodsInfo.status == 0">立即购买</div>
                                    <div class="goods-item-status" v-if="goodsInfo.status == 1" style="background: gray">已被购买</div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="message-item" v-for="(repo, index) in repoList" :key="repo.id">
                    <div class="message-item-user">
                        <div class="message-user-img">

                        </div>
                        <div class="message-user-text">
                            <div class="message-user-id">{{repo.replier}}</div>
                            <div v-if="repo.replierIsAdmin == 0">普通用户</div>
                            <div v-if="repo.replierIsAdmin == 1">管理员</div>
                            <div class="send-message-button" v-if="userId != repo.replierId">
                                <div @click="sendMessageShow(repo.replier, repo.id)">
                                    <i class="fa fa-commenting fa-lg"></i>发私信
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="message-item-text-container">
                        <div class="message-item-top-bar">
                            <div class="message-item-floor-num">#{{basePageSize+index+2}}</div>
                            <div>发布于{{new Date(repo.time*1000).toLocaleString()}}</div>
                            <div class="message-item-button-group">
                                <div @click="repoMessage(index+2)" v-if="goodsInfo.publisherId != userId">回复</div>
                                <div class="message-item-delete" v-if="isAdmin" @click="deleteRepo(repo.id)">删除</div>
                            </div>
                        </div>
                        <div class="message-item-main-body">
                            {{repo.text}}
                        </div>
                    </div>
                </div>
            </div>
            <Pagination :data="pagination" @numberChange="changeNum"></Pagination>
            <div class="repo-container">
                <textarea v-model="repoText" id="repoTextarea"></textarea>
                <div class="repo-button" @click="addRepo">回复</div>
            </div>
        </div>
        <ScrollTop></ScrollTop>
        <Footer></Footer>
        <div class="send-message-input" v-show="sendMessageStatus">
            <div class="send-message-container">
                <div class="send-meesage-close">
                    <i class="fa fa-close fa-lg" @click="sendMessageHidden"></i>
                </div>
                <textarea :placeholder="privateMessagePlaceholder" v-model="privateMessage"></textarea>
                <div class="send-message-submit" @click="sendPrivateMessage()">发送</div>
            </div>
        </div>
    </div>
</template>

<script>
import Header from '../components/Header'
import ScrollTop from '../components/ScrollTop'
import Pagination from '../components/Pagination'
import Footer from '../components/Footer'
export default {
    components: {
        Header,
        ScrollTop,
        Footer,
        Pagination,
    },
    data() {
        return {
            goodsInfo: {},
            goodsLargeImg: "",
            repoText: "",
            repoList: [],
            isAdmin: false,

            sendMessageStatus: false,
            privateMessagePlaceholder: "",
            sendMessageId: "",

            privateMessage: "",
            userId: this.getCookie('userid'),
            
            basePageSize: parseInt(this.$route.query.pageStart),
            pagination: {
                pages: 0,
                currentIndex: ((this.$route.query.pageStart)/this.constant.detailPageSize)+1,
            }
        }
    },
    watch: {
        '$route' (to, from) {
            this.$router.go(0)
        }
    },
    methods: {
        changeNum (data) {
            this.$router.push({path: '/goods', query: {
                id: this.$route.query.id,
                pageStart: (parseInt(data-1))*this.constant.detailPageSize,
            }})
        },
        sendMessageShow (user, id) {
            this.sendMessageStatus = true;
            this.privateMessagePlaceholder = "回复给"+user+":";
            this.sendMessageId = id;
        },
        sendMessageHidden () {
            this.sendMessageStatus = false;
        },
        sendPrivateMessage () {
            if(this.privateMessage == "") {
                alert('不能发送空消息');
            }
            else {
                const url = '/user/sendPrivateMessage';
                const data = {
                    fromId: this.getCookie('userid'),
                    toId: this.sendMessageId,
                    message: this.privateMessage,
                };
                this.myAjax_post(url, data).then(response => {
                    if(response.flag == 1){
                        alert(response.msg);
                        this.$router.go(0);
                    }
                });
            }
        },
        repoMessage: function(floor) {
            this.repoText = `回复${floor}楼: `;
            const repoTextarea = document.getElementById('repoTextarea');
            repoTextarea.focus();
            window.scrollTo(0, repoTextarea.offsetTop);
        },
        loginStatus () {
            this.isAdmin = (this.getCookie('admin') == 1 ? true : false);
        },
        getGoodsInfo () {
            const url = '/commodity/detail';
            const data = {
                commodityId: this.$route.query.id,
                pageSize: this.constant.detailPageSize,
            };
            this.myAjax_post(url, data).then(response => {
                if(response.flag == 1) {
                    this.goodsInfo = response.data;
                    this.goodsInfo.images = this.goodsInfo.images.split('@');
                    this.goodsLargeImg = this.goodsInfo.images[0];
                    this.pagination.pages = response.data.replyPages; 
                }
                else {
                    this.$router.push({path: '/index'});
                }
            });
        },
        showLargeImg (src) {
            this.goodsLargeImg = src;
        },
        getRepo () {
            const url = '/post/detail';
            const data = {
                commodityId: this.$route.query.id,
                pageSize: this.constant.detailPageSize,
                pageStart: this.$route.query.pageStart,
            };
            this.myAjax_post(url, data).then(response => {
                if(response.flag == 1) {
                    this.repoList = [];
                    for(let i in response.data) {
                        this.repoList.push(response.data[i]);
                    }
                }
            });
        },
        addRepo () {
            const url = '/post/reply';
            const data = {
                replier: this.getCookie('userid'),
                text: this.repoText,
                replyPostId: 0,
                commodityId: this.$route.query.id,
            };
            if(data.text == "" || data.text == undefined){
                alert('回复内容不能为空');
            }
            else{
                this.myAjax_post(url, data).then(response => {
                    if(response.flag == 1) {
                        alert(response.msg);
                        this.$router.go(0);
                    }
                    else {
                        alert(response.msg);
                    }
                });
            }
        },
        modifyGoods () {
            this.$router.push({path: '/modifypublish', query: {id: this.$route.query.id}});
        },
        deleteGoods () {
            if(this.isAdmin === true || this.goodsInfo.publisherId == this.userId ) {
                const url = '/commodity/delete';
                const data = {
                    commodityId: this.$route.query.id,
                    userId: this.getCookie('userid'),
                }
                this.myAjax_post(url, data).then(response => {
                    if(response.flag == 1) {
                        alert(response.msg);
                        this.getGoodsInfo();
                    }
                });
            }
            else {
                alert('您没有权限');
            }
        },
        deleteRepo: function(id) {
            if(this.isAdmin === true) {
                const url = '/post/delete';
                const data = {
                    postId: id,
                    userId: this.getCookie('userid'),
                };
                this.myAjax_post(url, data).then(response => {
                    if(response.flag == 1){
                        alert(response.msg);
                        this.$router.go(0);
                    }
                });
            }
        },
        buy () {
            const url = "/commodity/purchase";
            const data = {
                purchaserId: this.getCookie('userid'),
                commodityId: this.$route.query.id,
            };
            this.myAjax_post(url, data).then(response => {
                if(response.flag == 1){
                    alert(response.msg);
                    this.$router.go(0);
                }
            });
        },
    },
    created () {
        this.getGoodsInfo();
        this.getRepo();
        this.isAdmin = (this.getCookie('admin') == 1 ? true : false);
    }
}
</script>

<style scoped>
