    height: 100%;
    width: 100%;
    margin: 0;
    padding: 0;
}
.goods-main-body {
    min-height: 100%;
    height: auto;
    width: 100%;
    box-sizing: border-box;
    padding: 100px 100px 20px 100px;
}
.bread-nav {
    color: rgba(197, 60, 94, 1);
    margin-bottom: 40px;
}
.bread-nav > a,
.bread-nav > a:link,
.bread-nav > a:visited,
.bread-nav > a:hover,
.bread-nav > a:active {
    color: rgba(197, 60, 94, 1);
    text-decoration: none;
}
.goods-items-container {
    height: auto;
    width: 1200px;
}
.goods-item {
    height: 600px;
    width: 100%;
    border: 1px solid rgba(197, 60, 94, 1);
    border-radius: 5px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 20px 0;
}
.goods-item-user,
.message-item-user {
    height: 100%;
    width: 320px;
    border-right: 1px solid rgba(197, 60, 94, 1);
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.goods-user-img,
.message-user-img {
    height: 150px;
    width: 150px;
    border-radius: 5px;
    background: rgba(197, 60, 94, 1);
    margin-top: 60px;
    overflow: hidden;
}
.goods-user-img > img {
    height: 100%;
    width: 100%;
}
.goods-user-text {
    height: 300px;
    width: 200px;
    font-size: 14px;
    text-align: center;
}
.goods-user-text > div {
    color: black;
    height: 20px;
    box-sizing: border-box;
    padding: 0 20px;
}
.goods-user-text > .goods-user-id {
    height: 40px;
    width: 100%;
    line-height: 40px;
    text-align: center;
    font-size: 26px;
    color: rgba(197, 60, 94, 1);
    margin: 10px 0;
}
.goods-item-text-container,
.message-item-text-container {
    width: 880px;
    height: 100%;
}
.goods-item-top-bar,
.message-item-top-bar {
    height: 40px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: rgba(197, 60, 94, 1);
    box-sizing: border-box;
    padding: 0 20px;
    border-bottom: 1px solid rgba(197, 60, 94, 1);
}
.goods-item-floor-num,
.message-item-floor-num {
    font-size: 20px;
    font-weight: bold;
}
.message-item-button-group,
.goods-item-button-group {
    display: flex;
    justify-content: space-around;
    align-items: center;
    font-size: 14px;
}
.message-item-button-group > div,
.goods-item-button-group > div {
    cursor: pointer;
}
.goods-item-delete,
.message-item-delete {
    font-size: 14px;
    cursor: pointer;
    margin-left: 10px;
}
.goods-item-main-body {
    height: 560px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.goods-item-banner {
    height: 100%;
    width: 500px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    border-right: 1px solid rgba(197, 60, 94, 1);
}
.goods-large-img {
    height: 400px;
    width: 400px;
    background: rgba(197, 60, 94, 1);
}
.goods-large-img > img {
    height: 100%;
    width: 100%;
}
.goods-mini-imgs {
    height: 100px;
    width: 320px;
    display: flex;
    justify-content: space-around;
    align-items: center;
}
.goods-mini-imgs > div {
    height: 70px;
    width: 70px;
    border-radius: 2px;
    cursor: pointer;
    background: rgba(197, 60, 94, 1);
    box-sizing: border-box;
    overflow: hidden;
}
.goods-mini-imgs > div:hover {
    border: 2px solid rgba(197, 60, 94, 1);
}
.goods-mini-imgs > div > img {
    height: 100%;
    width: 100%;;
}
.goods-item-info {
    height: 100%;
    width: 380px;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: column;
}
.goods-item-title {
    font-size: 28px;
    height: 40px;
    width: 100%;
    line-height: 40px;
    box-sizing: border-box;
    padding-left: 20px;
    margin-top: 40px;
}
.goods-item-price {
    font-size: 26px;
    height: 40px;
    box-sizing: border-box;
    padding-left: 20px;
    color: rgba(197, 60, 94, 1);
}
.goods-item-text {
    height: 300px;
    width: 100%;
    box-sizing: border-box;
    padding: 20px;
    margin: 0;
    overflow: auto;
}
.goods-item-phone,
.goods-item-qq,
.goods-item-wechat {
    margin: 20px;
}
.goods-item-phone {
    line-height: 20px;
}
.goods-item-bottom-bar {
    height: 140px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.goods-item-contact-group {
    height: 100%;
    width: 180px;
    border-top: 1px solid rgba(197, 60, 94, 1);
}
.goods-item-status {
    height: 140px;
    width: 199px;
    background: rgba(197, 60, 94, 1);
    color: white;
    font-size: 40px;
    line-height: 140px;
