    box-sizing: border-box;
    padding: 0 20px;
    text-align: center;
}
.message-user-text > div {
    height: 20px;
    width: 100%;
    margin: 5px 0;
    width: 100%;
    line-height: 20px;
    text-align: center;
    font-size: 14px;
}
.message-user-text > .message-user-id {
    font-size: 24px;
    color: rgba(197, 60, 94, 1);
    height: 40px;
    line-height: 40px;
    text-align: center;
}
.message-item-main-body {
    height: 260px;
    width: 100%;
    box-sizing: border-box;
    padding: 20px;
    overflow: auto;
}
.repo-container {
    height: 200px;
    width: 100%;
    padding: 0 85px;
    box-sizing: border-box;
    text-align: center;
    margin: 100px 0;
}
.repo-container > textarea {
    height: 170px;
    width: 100%;
    box-sizing: border-box;
    border-radius: 5px;
    padding: 20px;
}
.repo-container > textarea:focus {
    outline: none;
}
.repo-button {
    height: 30px;
    width: 80px;
    line-height: 30px;
    text-align: center;
    border-radius: 5px;
    background: rgba(197, 60, 94, 1);
    color: white;
