    border: 1px solid gray;
}
.send-message-container > textarea:focus {
    outline: none;
}
.send-meesage-close {
    width: 100%;
    text-align: right;
}
.send-message-submit {
    padding: 5px 10px;
    border-radius: 5px;
    text-align: center;
    background: white;
    color: rgba(197, 60, 94, 1);
}
</style>


