<template>
    <div class="container">
        <Header></Header>
        <div class="modify-info-container">
            <div class="input-group">
                <div v-show="warningStatus">两次输入的密码不一致</div>
                <input type="password" placeholder="旧密码" v-model="oldPassword" class="input-item"/>
                <input type="password" placeholder="新密码" v-model="newPassword" class="input-item"/>
                <input type="password" placeholder="重复新密码" v-model="newPasswordRepeat" class="input-item"/>
            </div>
            <div class="save-modify-button" @click="updatePassword">修改</div>
        </div>
        <ScrollTop></ScrollTop>
        <Footer></Footer>
    </div>
</template>

<script>
import Header from '../components/Header'
import ScrollTop from '../components/ScrollTop'
import Footer from '../components/Footer'
export default {
    components: {
        Header,
        ScrollTop,
        Footer
    },
    data () {
        return {
            oldPassword: "",
            newPassword: "",
            newPasswordRepeat: "",
            warningStatus: false,
        }
    },
    watch: {
        newPasswordRepeat () {
            if(this.newPasswordRepeat != this.newPassword) {
                this.warningStatus = true;
            }
            else {
                this.warningStatus = false;
            }
        }
    },
    methods: {
        updatePassword () {
            if(this.newPassword == this.newPasswordRepeat) {
                const url = '/user/modifyPw';
                const data = {
                    userId: this.getCookie('userid'),
                    oldPw: this.oldPassword,
                    newPw: this.newPassword
                };
                this.myAjax_post(url, data).then(response => {
                    if(response.flag == 1){
                        alert(response.msg);
                        if(response.msg == "更新成功！") {
                            this.userLogout();
                        }
                    }
                });
            }
            else {
                alert('两次输入的密码不一致');
            }
        },
        userLogout () {
            let url = '/logout';
            let data = "";
            this.myAjax_get(url, data).then(response => {
                if(response.flag == 1){
                    this.unsetCookie('login');
                    this.unsetCookie('username');
                    this.unsetCookie('admin');
                    this.unsetCookie('userid');
                }
            });
        },
    },
    created () {
        if(!this.checkCookie('login')) {
            this.$router.push({path: '/index'});
        }
    }

}
</script>

<style scoped>
.container {
    height: 100%;
    width: 100%;
}
.modify-info-container {
    min-height: 100%;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin: 20px 0;
    box-sizing: border-box;
    padding: 100px;
}
.modify-head-container {
    height: 200px;
    width: 200px;
    border-radius: 50%;
    overflow: hidden;
    position: relative;
}
.input-item {
    height: 40px;
    width: 240px;
    margin: 20px 0;
    text-align: center;
}
.save-modify-button {
    height: 30px;
    width: 60px;
    border-radius: 5px;
    background: rgba(197, 60, 94, 1);
    color: white;
    line-height: 30px;
    text-align: center;
    margin: 40px 0;
    cursor: pointer;
}

.input-group {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
}
input:focus {
    outline: none;
}
</style>


