<template>
<div class="institute-manage-container">
    <div class="institute-manage-title">学院设置</div>
    <div class="institute-manage">
        <div class="institute-manage-add-group">
            <input type="text" placeholder="输入学院名称以添加新学院" id="add-institute-content" v-model="addInstituteName">
            <div id="add-institute-button" @click="addInstitute">添加</div>
        </div>
        <div class="institute-manage-add-group">
            <input type="text" placeholder="点击编辑修改学院名称" id="modify-institute-content" v-model="instituteName">
            <div id="add-institute-button" @click="modifyInstitute">修改</div>
        </div>
        <div class="institute-manage-list">
            <div class="institute-item" v-for="(item, index) in institute" :key="index">
                <div>{{item}}</div>
                <div class="institute-item-button-group">
                    <div @click="chooseInstitute(index)">编辑</div>
                    <div @click="deleteInstitute(index)">删除</div>
                </div>
            </div>
        </div>
    </div>
</div>
</template>

<script>
    export default {
        data() {
            return {
                institute: [],
                addInstituteName: "",
                instituteName: "",
                instituteId: "",
            }
        },
        methods: {
            getInstitute () {
                let url = '/index/institute';
                this.myAjax_get(url).then(response => {
                    this.institute = [];
                    if(response.flag == 1) {
                        this.institute = response.data;
                    }
                });
            },
            chooseInstitute (id) {
                this.instituteId = id;
                document.getElementById('modify-institute-content').focus();
            },
            modifyInstitute () {
                const url = '/user/admin/renameInstitute';
                const data = {
                    instituteId: this.instituteId,
                    newName: this.instituteName,
                    userId: this.getCookie('userid'),
                };
                this.myAjax_post(url, data).then(response => {
                    if(response.flag == 1){
                        alert(response.msg);
                        this.getInstitute();
                    }
                })
            },
            deleteInstitute (id) {
                const url = '/user/admin/deleteInstitute';
                const data = {
                    instituteId: id,
                    userId: this.getCookie('userid'),
                };
                this.myAjax_post(url, data).then(response => {
                    if(response.flag == 1){
                        alert(response.msg);
                        this.$router.go(0);
                    }
                })
            },
            addInstitute () {
                if(this.addInstituteName != "") {
                    const url = '/user/admin/addInstitute';
                    const data = {
                        instituteName: this.addInstituteName,
                        userId: this.getCookie('userid'),
                    };
                    this.myAjax_post(url, data).then(response => {
                        if(response.flag == 1){
                            alert(response.msg);
                            this.$router.go(0);
                        }
                    })
                }
                else {
                    alert("学院名不能为空");
                }
            }
        },
        created () {
            this.getInstitute();
        }
    }
</script>

<style scoped>
.institute-manage-container {
    height: 500px;
    width: 380px;
    /* background: rgba(197, 60, 94, 1); */
    border: 2px solid rgba(197, 60, 94, 1);;
    margin: 100px 20px 20px 20px;
    /* border-radius: 5px; */
}
.institute-manage-title {
    height: 30px;
    width: 100%;
    line-height: 30px;
    box-sizing: border-box;
    padding: 0 20px;
    font-size: 18px;
    color: rgba(197, 60, 94, 1);
    margin: 20px 0 10px 0;
}
.add-institute-warning {
    font-size: 12px;
    color: rgb(248, 50, 50);
    margin: 0 20px;
}
.institute-manage-add-group {
    height: 50px;
    width: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}
#add-institute-content,
#modify-institute-content {
    height: 30px;
    width: 200px;
    box-sizing: border-box;
    padding: 0 10px;
    box-shadow: none;
    text-align: left;
    margin: 0 20px;
    border: 1px solid gray;
}
#modify-institute-content:focus,
#add-institute-content:focus {
    outline: none;
    border: none;
    border: 1px solid gray;
}
#add-institute-button {
    height: 30px;
    width: 50px;
    line-height: 30px;
    text-align: center;
    background: rgba(197, 60, 94, 1);
    color: white;
    /* border-radius: 5px; */
    font-size: 12px;
    cursor: pointer;
}
.institute-manage-list {
    height: 310px;
    width: 100%;
    box-sizing: border-box;
    padding: 10px;
    margin: 10px 0;
    overflow: auto;
}
.institute-manage-list > span:hover .institute-delete {
    display: block;
}
.institute-delete {
    height: 15px;
    width: 15px;
    border-radius: 50%;
    font-size: 8px;
    background: white;
    color: rgba(197, 60, 94, 1);
    line-height: 15px;
    text-align: center;
    position: absolute;
    top: -7px;
    right: -7px;
    box-sizing: border-box;
    box-shadow: 0 0 2px black;
    display: none;
    cursor: pointer;
}
.institute-item {
    height: 30px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 10px 0;
}
.institute-item-button-group {
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.institute-item-button-group > div {
    margin: 0 5px;
    cursor: pointer;
    font-size: 12px;
}
</style>


