<template>
<div class="tag-manage-container">
    <div class="tag-manage-title">分类管理</div>
    <div class="tag-manage">
        <div class="tag-manage-add-group">
            <input type="text" placeholder="添加类别" id="add-tag-content" v-model="tagName" @keydown.enter="addClassification">
            <div class="add-tag-button"  @click="addClassification">添加</div>
        </div>
        <div class="tag-manage-add-group">
            <input type="text" placeholder="点击编辑以修改分类名" id="modify-tag-content" v-model="newName">
            <div class="add-tag-button"  @click="modifyClassification">修改</div>
        </div>
        <div class="tag-manage-list">
            <div v-for="tag in classifications" :key="tag.id" class="tag-manage-item">
                <div>
                    {{tag.name}}
                </div>
                <div class="tag-manage-button-group">
                    <div @click="chooseClassification(tag.id)">编辑</div>
                    <div @click="removeClassification(tag.id)">删除</div>
                </div>
            </div>
        </div>
    </div>
</div>
</template>

<script>
export default {
    data() {
        return {
            classifications: [],
            tagName: "",
            newName: "",
            classificationsId: 0,
        }
    },
    methods: {
        getClassifications () {
            this.myAjax_get('/index/classification').then(response =>{
                if(response.flag == 1) {
                    this.classifications = [];
                    for(let i in response.data) {
                        this.classifications.push(response.data[i]);
                    }
                }
            });
        },
        addClassification () {
            let data = {
                name: this.tagName,
                refer: 0,
                userId: this.getCookie('userid'),
            };
            let url = "/user/admin/addClassification";
            this.myAjax_post(url, data).then(response => {
                if(response.flag == 1) {
                    alert(response.msg);
                    this.getClassifications ()
                }
            });
        },
        removeClassification (id) {
            let data = {
                classificationId: id,
                userId: this.getCookie('userid'),
            };
            let url = "/user/admin/deleteClassification";
            this.myAjax_post(url, data).then(response => {
                if(response.flag == 1) {
                    alert(response.msg);
                    this.getClassifications ()
                }
            });
        },
        chooseClassification (id) {
            this.classificationsId = id;
            document.getElementById('modify-tag-content').focus();
        },
        modifyClassification () {
            if(this.newName != ""){
                const url = '/user/admin/renameClassification';
                const data = {
                    classificationId: this.classificationsId,
                    newName: this.newName,
                    userId: this.getCookie('userid'),
                };
                this.myAjax_post(url, data).then(response => {
                    if(response.flag == 1){
                        alert(response.msg);
                        this.getClassifications();
                    }
                })
            }
            else {
                alert('新名字不能为空');
            }
        }
    },
    created () {
        this.getClassifications();
    }
}
</script>

<style scoped>
.tag-manage-container {
    height: 500px;
    width: 380px;
    /* background: rgba(197, 60, 94, 1); */
    border: 2px solid rgba(197, 60, 94, 1);
    margin: 100px 20px 20px 20px;
    /* border-radius: 5px; */
}
.tag-manage-title {
    height: 30px;
    width: 100%;
    line-height: 30px;
    box-sizing: border-box;
    padding: 0 20px;
    font-size: 18px;
    color: rgba(197, 60, 94, 1);
    margin: 20px 0 10px 0;
}
.tag-manage-add-group {
    height: 50px;
    width: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}
#add-tag-content,
#modify-tag-content {
    height: 30px;
    width: 200px;
    /* border: none; */
    box-sizing: border-box;
    box-shadow: none;
    text-align: left;
    margin: 0 20px;
    border: 1px solid gray;
}
#modify-tag-content:focus,
#add-tag-content:focus {
    outline: none;
    border: none;
    border: 1px solid gray;
}
.add-tag-button {
    height: 30px;
    width: 50px;
    line-height: 30px;
    text-align: center;
    background: rgba(197, 60, 94, 1);
    color: white;
    /* border-radius: 5px; */
    font-size: 12px;
    cursor: pointer;
}
.tag-manage-list {
    height: 360px;
    width: 100%;
    box-sizing: border-box;
    padding: 10px;
    margin: 10px 0;
    overflow: auto;
}
.tag-manage-list > span {
    margin: 10px;
    font-size: 14px;
    background: rgba(197, 60, 94, 1);
    color: white;
    /* border-radius: 5px; */
    padding: 5px;
    float: left;
    position: relative;
    cursor: default;
}
.tag-manage-list > span:hover .tag-delete {
    display: block;
}
.tag-delete {
    height: 15px;
    width: 15px;
    border-radius: 50%;
    font-size: 8px;
    background: white;
    color: rgba(197, 60, 94, 1);
    line-height: 15px;
    text-align: center;
    position: absolute;
    top: -7px;
    right: -7px;
    box-sizing: border-box;
    box-shadow: 0 0 2px black;
    display: none;
    cursor: pointer;
}
.tag-manage-item {
    height: 30px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.tag-manage-button-group {
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.tag-manage-button-group > div {
    font-size: 12px;
    cursor: pointer;
    margin: 0 5px;
}
</style>


