<template>
<div class="board-manage-container">
    <div class="board-manage-title">公告列表</div>
    <div class="board-manage">
        <div class="add-board-warning" v-if="noticeArray.length == 0">尚无公告</div>
        <div class="board-manage-list">
            <div class="board-manage-item" v-for="item in noticeArray" :key="item.id">
                <div class="board-manage-item-title">{{item.title}}</div>
                <div class="board-manage-item-button" @click="deleteNotice(item.id)">
                    删除 
                </div>
            </div>
        </div>
    </div>
</div>
</template>

<script>
    export default {
        data() {
            return {
                noticeArray: [],
            }
        },
        methods: {
            getNotice () {
                const url = '/index/sixNoticeTitle';
                this.myAjax_get(url).then(response => {
                    this.noticeArray = response.data;
                });
            },
            deleteNotice: function (id) {
                const url = '/user/admin/deleteNotice';
                const data = {
                    noticeId: id,
                    userId: this.getCookie('userid'),
                };
                this.myAjax_post(url, data).then(response => {
                    if(response.flag == 1){
                        alert(response.msg);
                        this.$router.go(0);
                    }
                });
            }
        },
        created () {
            this.getNotice();
        }
    }
</script>

<style scoped>
.board-manage-container {
    height: 500px;
    width: 380px;
    /* background: rgba(197, 60, 94, 1); */
    border: 2px solid rgba(197, 60, 94, 1);
    margin: 100px 20px 20px 20px;
    /* border-radius: 5px; */
}
.board-manage-title {
    height: 30px;
    width: 100%;
    line-height: 30px;
    box-sizing: border-box;
    padding: 0 20px;
    font-size: 18px;
    color: rgba(197, 60, 94, 1);
    margin: 20px 0 10px 0;
}
.add-board-warning {
    font-size: 12px;
    color: rgb(248, 50, 50);
    margin: 0 20px;
}
.board-manage-add-group {
    height: 50px;
    width: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}
#add-board-content {
    height: 30px;
    width: 200px;
    /* border: none; */
    box-shadow: none;
    text-align: left;
    margin: 0 20px;
    border: 1px solid gray;
}
#add-board-content:focus {
    outline: none;
    border: none;
    border: 1px solid gray;
}
#add-board-button {
    height: 30px;
    width: 50px;
    line-height: 30px;
    text-align: center;
    background: rgba(197, 60, 94, 1);
    color: white;
    /* border-radius: 5px; */
    font-size: 12px;
    cursor: pointer;
}
.board-manage-list {
    height: 360px;
    width: 100%;
    box-sizing: border-box;
    padding: 10px;
    margin: 10px 0;
    overflow: auto;
}
.board-manage-item {
    height: 30px;
    width: 100%;
    box-sizing: border-box;
    padding: 0 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.board-manage-item:nth-child(2n) {
    background: rgb(194, 192, 192);
}
.board-manage-item-button {
    font-size: 12px;
    cursor: pointer;
}
</style>


