<template>
    <div class="container">
        <Header></Header>
        <div class="search-result-container">
            <div class="search-title">
                搜索内容： {{searchText}}
            </div>
            <div class="underline"></div>
            <div class="search-result">
                <Goods v-for="item in searchArray" :key="item.id" :data="item"></Goods>
            </div>
        </div>
        <Pagination :data="pagination" @numberChange="changeNum"></Pagination>
        <ScrollTop></ScrollTop>
        <Footer></Footer>
    </div>
</template>

<script>
import Header from '../components/Header'
import Goods from '../components/Goods'
import Pagination from '../components/Pagination'
import ScrollTop from '../components/ScrollTop'
import Footer from '../components/Footer'
export default {
    components: {
        Header,
        ScrollTop,
        Footer,
        Goods,
        Pagination,
    },
    data() {
        return {
            searchText: this.$route.query.search,
            searchArray: [],
            pagination: {
                pages: 0,
                currentIndex: parseInt(this.$route.query.pageStart/this.$route.query.pageSize)+1,
            }
        }
    },
    watch: {
        '$route' (to, from) {
            this.$router.go(0);
        } 
    },
    methods: {
        changeNum (id) {
            this.$router.push({
                path: '/search',
                query: {
                    search: this.$route.query.search,
                    institute: this.$route.query.institute,
                    classification: this.$route.query.classification,
                    pageStart: (parseInt(id)-1)*this.$route.query.pageSize,
                    pageSize: this.$route.query.pageSize,
                }
            })
        },
        search () {
            const url = '/index/search';
            const data = {
                search: this.$route.query.search,
                institute: this.$route.query.institute,
                classification: this.$route.query.classification,
                pageStart: this.$route.query.pageStart,
                pageSize: this.$route.query.pageSize,
            }
            this.myAjax_post(url, data).then(response => {
                if(response.flag == 1){
                    this.searchArray = [];
                    for(let i in response.data) {
                        this.searchArray.push(response.data[i]);
                        this.$route.query.pageStart = data.pageStart;
                    }
                }
            });
        },
        searchNum () {
            const url = '/index/getSearchPages';
            const data = {
                search: this.$route.query.search,
                institute: this.$route.query.institute,
                classification: this.$route.query.classification,
                pageSize: this.$route.query.pageSize
            };
            this.myAjax_post(url, data).then(response => {
                if(response.flag == 1){
                    this.pagination.pages = response.data.pages;
                }
            });
        }
    },
    created () {
        this.searchNum();
        this.search();
    }
}
</script>

<style scoped>
.container {
    height: 100%;
    width: 100%;
}
.search-result-container {
    min-height: 100%;
    width: 100%;
    padding: 100px;
    box-sizing: border-box;
}
.search-title {
    font-size: 30px;
    color: rgba(197, 60, 94, 1);
}
.underline {
    height: 2px;
    width: 95%;
    background: rgba(197, 60, 94, 1);
    margin: 10px 0;
}
</style>


