<template>
    <div class="container">
        <Header></Header>
        <div class="message-item-container">
            <Reply :data="item" v-for="item in messageArray" :key="item.id"></Reply>
        </div>
        <ScrollTop></ScrollTop>
        <Footer></Footer>
    </div>
</template>

<script>
import Header from '../components/Header';
import Reply from '../components/Reply';
import ScrollTop from '../components/ScrollTop';
import Footer from '../components/Footer';

export default {
    components: {
        Header,
        ScrollTop,
        Footer,
        Reply,
    },
    data () {
        return {
            messageArray: [],
            data: {
                url: 'goods',
                goodsImg: '../../static/images/head-icon.jpg',
                goodsTitle: 'iPhone X 99新',
                replyName: 'Tong',
                replyText: '多少G的版本？',
                replyTime: '2018/04/08/02:00',
            }
        }
    },
    methods: {
        getMessage () {
            const url = '/user/myPrivateMessage';
            const data = {
                userId: this.getCookie('userid'),
            };
            this.myAjax_post(url, data).then(response => {
                if(response.flag == 1){
                    for(let i in response.data) {
                        this.messageArray.push(response.data[i]);
                    }
                }
            });
        }
    },
    created () {
        this.getMessage();
    }
}
</script>

<style scoped>
.container {
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
}
.message-item-container {
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    padding:  100px 100px 20px 100px;
    overflow: auto;
}
</style>


