<template>
    <div class="container">
        <Header @login="loginStatus"></Header>
        <div class="mine-main-container">
            <div class="main-head-container">
                <img :src="userInfo.avatar">
            </div>
            <div class="main-info-group">
                <div>姓名</div>
                <div>{{userInfo.name}}</div>
            </div>
            <div class="main-info-group">
                <div>用户名</div>
                <div>{{userInfo.username}}</div>
            </div>
            <div class="main-info-group">
                <div>UID</div>
                <div>{{userInfo.id}}</div>
            </div>
            <div class="main-info-group">
                <div>手机</div>
                <div>{{userInfo.phone}}</div>
            </div>
            <div class="main-info-group">
                <div>学院</div>
                <div>{{userInfo.institute}}</div>
            </div>
            <div class="main-info-group">
                <div>权限</div>
                <div v-if="userInfo.admin != true">普通用户</div>
                <div v-if="userInfo.admin == true">管理员</div>
            </div>
            <div class="main-info-group">
                <div>注册时间</div>
                <div>{{new Date(userInfo.time*1000).toLocaleString()}}</div>
            </div>
            <div class="main-info-button-group">
                <router-link to="/modifyinfo" tag="div">
                    修改个人信息
                </router-link>
                <router-link to="/modifypassword" tag="div">
                    修改账户密码
                </router-link>
            </div>
            <div class="already-buy">
                <div id="buyPie"></div>
                <el-date-picker
                v-model="value6"
                type="daterange"
                range-separator="至"
                start-placeholder="开始日期"
                end-placeholder="结束日期">
                </el-date-picker>
            </div>
            <div class="publish-info-group">
                <div class="publish-info-title">
                    <div class="publish-button" @click="showPublish">已发布的商品</div>
                    <div class="alreadybuy-button" @click="showAlreadyBuy">已购买的商品</div>
                </div>
                <div class="publish-goods-container" v-show="showMode == 'publish'">
                    <Published :data="publish" v-for="publish in publishArray" :key="publish.id"></Published>
                    <Pagination :data="publishPagination" @numberChange="publishChangeNum"></Pagination>
                </div>
                <div class="publish-goods-container" v-show="showMode == 'buy'">
                    <Goods v-for="item in alreadyBuyArray" :data="item" :key="item.id"></Goods>
                    <Pagination :data="buyPagination" @numberChange="buyChangeNum"></Pagination>
                </div>
            </div>
        </div>
        <ScrollTop></ScrollTop>
        <Footer></Footer>
    </div>
</template>

<script>
import Header from '../components/Header'
import ScrollTop from '../components/ScrollTop'
import Pagination from '../components/Pagination'
import Footer from '../components/Footer'
import Published from '../components/Published'
import Goods from '../components/Goods'
export default {
    components: {
        Header,
        ScrollTop,
        Footer,
        Published,
        Goods,
        Pagination,
    },
    data () {
        return {
            userInfo: {},
            showMode: this.$route.query.mode,
            value6: "",
            publishArray: [],
            alreadyBuyArray: [],
            alreadyBuyPieArray: [],
            publishPagination: {
                pages: 0,
                currentIndex: parseInt(this.$route.query.publishPageStart/this.constant.publishPageSize)+1,
            },
            buyPagination: {
                pages: 0,
                currentIndex: parseInt(this.$route.query.buyPageStart/this.constant.buyPageSize)+1,
            }
        }
    },
    methods: {
        publishChangeNum (id) {
            this.$router.push({path: '/mine', query: {
                    publishPageStart: (parseInt(id)-1)*this.constant.publishPageSize,
                    buyPageStart: this.$route.query.buyPageStart,
                    mode: 'publish',
                }
            });
            this.$router.go(0);
        },
        buyChangeNum (id) {
            this.$router.push({path: '/mine', query: {
                    publishPageStart: this.$route.query.publishPageStart,
                    buyPageStart: (parseInt(id)-1)*this.constant.buyPageSize,
                    mode: 'buy',
                }
            });
            this.$router.go(0);
        },
        showPublish () {
            this.showMode = 'publish';
        },
        showAlreadyBuy () {
            this.showMode = 'buy';
        },
        checkLogin () {
            if(!this.checkCookie('login')) {
                this.$router.push({path: '/index'});
            }
        },
        getInfo () {
            const url = '/user/detail/';
            const data = {
                userId: this.getCookie('userid'),
            };
            this.myAjax_post(url, data).then(response => {
                if(response.flag == 1){
                    this.userInfo = response.data;
                }
            });
        },
        loginStatus: function(data) {
            if(data == 'false') {
                this.checkLogin();
            }
        },
        getAlreadyBuy () {
            const url = '/user/bought';
            const data = {
                userId: this.getCookie('userid'),
                pageStart: this.$route.query.buyPageStart,
                pageSize: this.constant.buyPageSize,
            };
            this.myAjax_post(url, data).then(response => {
                if(response.flag == 1) {
                    for(let i in response.data) {
                        this.alreadyBuyArray.push(response.data[i]);
                    }
                    console.log(response);
                }
            });
        },
        getAlreadyBuyNum () {
            const url = '/user/boughtPages';
            const data = {
                userId: this.getCookie('userid'),
                pageSize: this.constant.buyPageSize,
            };
            this.myAjax_post(url, data).then(response => {
                if(response.flag == 1){
                    this.buyPagination.pages = response.data.pages;
                }
            })
        },
        getPublish () {
            const url = '/user/myPublish';
            const data = {
                userId: this.getCookie('userid'),
                pageSize: this.constant.publishPageSize,
                pageStart: this.$route.query.publishPageStart,
            };
            this.myAjax_post(url, data).then(response => {
                if(response.flag == 1){
                    for(let i in response.data) {
                        this.publishArray.push(response.data[i]);
                    }
                }
            });
        },
        getPublishNum () {
            const url = '/user/commodityPages';
            const data = {
                userId: this.getCookie('userid'),
                pageSize: this.constant.publishPageSize,
            };
            this.myAjax_post(url, data).then(response => {
                if(response.flag == 1){
                    this.publishPagination.pages = response.data.pages;
                }
            });
        },
        getShoppingPie () {
            let start = Date.parse(this.value6[0])/1000;
            let end =  Date.parse(this.value6[1])/1000;
            const url = '/user/timeShopping';
            const data = {
                start: start,
                end: end,
                userId: this.getCookie('userid'),
            };
            this.myAjax_post(url, data).then(response => {
                if(response.flag == 1){
                    this.alreadyBuyPieArray = [];
                    for(let i in response.data) {
                        this.alreadyBuyPieArray.push(response.data[i]);
                    }
                    this.eChartsInit();
                }
            });
        },
        eChartsInit () {
            let option = {
                backgroundColor: '#ffffff',

                title: {
                    text: '购买详情',
                    left: 'center',
                    top: 20,
                    textStyle: {
                        color: '#ccc'
                    }
                },

                tooltip : {
                    trigger: 'item',
                    formatter: "{a} <br/>{b} : {c} ({d}%)"
                },

                visualMap: {
                    show: false,
                    min: 80,
                    max: 600,
                    inRange: {
                        colorLightness: [0, 1]
                    }
                },
                series : [
                    {
                        name:'购买占比',
                        type:'pie',
                        radius : '55%',
                        center: ['50%', '50%'],
                        data: this.alreadyBuyPieArray.sort(function (a, b) { return a.value - b.value; }),
                        roseType: 'radius',
                        label: {
                            normal: {
                                textStyle: {
                                    color: 'rgba(255, 255, 255, 0.3)'
                                }
                            }
                        },
                        labelLine: {
                            normal: {
                                lineStyle: {
                                    color: 'rgba(255, 255, 255, 0.3)'
                                },
                                smooth: 0.2,
                                length: 10,
                                length2: 20
                            }
                        },
                        itemStyle: {
                            normal: {
                                color: '#c23531',
                            }
                        },

                        animationType: 'scale',
                        animationEasing: 'elasticOut',
                        animationDelay: function (idx) {
                            return Math.random() * 200;
                        }
                    }
                ]
            };
            let echarts = require('echarts/lib/echarts');
            require("echarts/lib/chart/pie");
            require("echarts/lib/component/tooltip");

            let myChart = echarts.init(document.getElementById('buyPie'));
            myChart.setOption(option);
        },
    },
    watch: {
        value6: function() {
            this.getShoppingPie();
        }
    },
    created () {
        if(!this.checkCookie('login')) {
            this.$router.push({path: '/index'});
        }
        else {
            this.getInfo();
            this.getPublishNum();
            this.getPublish();
            this.getAlreadyBuyNum();
            this.getAlreadyBuy();
        }
    },
}
</script>

<style scoped>
#buyPie {
    height: 400px;
    width: 400px;
    margin-bottom: 10px;
}
.already-buy {
    position: absolute;
    top: 0;
    right: 0;
    height: auto;
    width: 400px;
}
.container {
    min-height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
}
.mine-main-container {
    height: auto;
    width: 1000px;
    margin: 100px 0;
    box-sizing: border-box;
    padding: 20px;
    border-radius: 5px;
    position: relative;
}
.main-info-group {
    color: rgba(197, 60, 94, 1);
    font-size: 20px;
    width: 400px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 20px;
}
.main-info-group > div:last-of-type {
    color: black;
    font-size: 18px;
}
.main-head-container {
    height: 100px;
    width: 100px;
    border-radius: 50%;
    background: rgba(197, 60, 94, 1);
    overflow: hidden;
}
.main-head-container > img {
    height: 100%;
    width: 100%;
}
.publish-info-group {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
}
.publish-info-title {
    width: 100%;
    height: 40px;
    font-size: 20px;
    box-sizing: content-box;
    padding-left: 40px;
    color: rgba(197, 60, 94, 1);
    margin-top: 40px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}
.publish-info-title > div {
    margin: 0 10px;
    padding: 0 10px;
    cursor: pointer;
}
.publish-goods-container {
    width: 100%;
    height: auto;
    box-sizing: border-box;
}
.publish-goods-item {
    width: 100%;
    height: 200px;
    border-radius: 5px;
    background: rgba(197, 60, 94, 1);
    margin: 40px 0;
}
.main-info-button-group {
    width: 100%;
    height: 30px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin: 20px 0;
    box-sizing: border-box;
    padding: 20px;
}
.main-info-button-group > div {
    height: 30px;
    width: 100px;
    border-radius: 5px;
    background: rgba(197, 60, 94, 1);
    color: white;
    line-height: 30px;
    text-align: center;
    margin-left: 20px;
    font-size: 14px;
    cursor: pointer;
}
</style>


