<template>
    <div class="board-editor-container">
        <div class="board-editor-title">公告编辑</div>
        <input type="text" placeholder="公告标题" class="board-title" v-model="title"/>
        <textarea rows="20" placeholder="编辑正文" class="board-content" v-model="text"></textarea>
        <div class="board-editor-submit" @click="addNotice">发布</div>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                title: "",
                text: "",
            }
        },
        methods: {
            addNotice () {
                const url = '/user/admin/publishNotice';
                const data = {
                    title: this.title,
                    text: this.text,
                    userId: this.getCookie('userid'),
                };
                this.myAjax_post(url, data).then(response => {
                    if(response.flag == 1){
                        alert(response.msg);
                        this.$router.go(0);
                    }
                });
            }
        }
    }
</script>

<style scoped>
.board-editor-container {
    height: 500px;
    width: 804px;
    margin: 100px 20px 20px 20px;
    border: 2px solid rgba(197, 60, 94, 1);
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
}
.board-editor-title {
    font-size: 18px;
    color: rgba(197, 60, 94, 1);
    margin: 20px;
}
.board-title {
    height: 30px;
    width: 200px;
    margin: 20px;
    border: 1px solid gray;
}
.board-content {
    width: 760px;
    margin: 20px;
}
.board-title:focus,
.board-content:focus {
    outline: none;
    border: 1px solid gray;
}
.board-editor-submit {
    height: 30px;
    width: 50px;
    background: rgba(197, 60, 94, 1);
    color: white;
    line-height: 30px;
    text-align: center;
    font-size: 14px;
    margin: 20px;
    cursor: pointer;
}
</style>


