<template>
    <div class="message-item">
        <div class="massage-item-container"  @click="sendMessageShow">
            <div class="goods-img-container">
                <img :src="data.goodsImg">
            </div>
            <div class="message-text-container">
                <div class="message-text">{{ data.fromUserName }} 给你发消息： {{ data.message }}</div>
                <div class="message-time">{{new Date(data.time*1000).toLocaleString()}}</div>
            </div>
        </div>
        <div class="delete-message" @click="deleteMessage(data.id)">
            <i class="fa fa-close fa-3x"></i>
        </div>
        <div class="send-message-input" v-show="sendMessageStatus">
            <div class="send-message-container">
                <div class="send-meesage-close">
                    <i class="fa fa-close fa-lg" @click="sendMessageHidden"></i>
                </div>
                <textarea :placeholder="'回复给'+data.fromUserName+':'" v-model="privateMessage"></textarea>
                <div class="send-message-submit" @click="sendPrivateMessage(data.fromId)">发送</div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        props: {
            data: Object,
        },
        data() {
            return {
                sendMessageStatus: false,
                privateMessage: "",
            }
        },
        methods: {
            sendPrivateMessage: function(id) {
                if(this.privateMessage == "") {
                    alert('不能发送空消息');
                }
                else {
                    const url = '/user/sendPrivateMessage';
                    const data = {
                        fromId: this.getCookie('userid'),
                        toId: id,
                        message: this.privateMessage,
                    };
                    this.myAjax_post(url, data).then(response => {
                        if(response.flag == 1){
                            alert(response.msg);
                            this.$router.go(0);
                        }
                    });
                }
            },
            sendMessageHidden () {
                this.sendMessageStatus = false;
            },
            sendMessageShow () {
                this.sendMessageStatus = true;
            },
            deleteMessage: function(id) {
                const url = '/user/deletePrivateMessage';
                const data = {
                    messageId: id,
                    userId: this.getCookie('userid'),
                };
                this.myAjax_post(url, data).then(response => {
                    if(response.flag == 1){
                        alert(response.msg);
                        this.$router.go(0);
                    }
                });
            }
        }
    }
</script>

<style scoped>
.message-item {
    height: 150px;
    width: 100%;
    border-radius: 5px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    cursor: pointer;
    margin: 20px 0;
}
.massage-item-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.goods-img-container {
    height: 120px;
    width: 120px;
    border-radius: 5px;
    overflow: hidden;
    background: rgba(197, 60, 94, 1);
    margin: 15px;
}
.goods-img-container > img {
    height: 100%;
    width: 100%;
}
.message-text-container {
    height: 100%;
    max-width: 880px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
}
.goods-title {
    height: 30px;
    width: 100%;
    line-height: 15px;
    text-align: left;
}
.message-text {
    font-size: 24px;
    color: rgba(197, 60, 94, 1);
    margin: 15px 0;
}
.message-time {
    font-size: 16px;
    color: gray;
    margin: 15px 0;
}
.send-message-input {
    height: 100%;
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    background: rgba(192, 192, 192, 0.397);
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 12px;
    z-index: 20;
}
.send-message-container {
    height: 200px;
    width: 300px;
    box-sizing: border-box;
    padding: 10px;
    border-radius: 2px;
    background: rgba(197, 60, 94, 1);
    color: white;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
}
.send-meesage-close {
    width: 100%;
    text-align: right;
}
.send-message-submit {
    padding: 5px 10px;
    border-radius: 5px;
    text-align: center;
    background: white;
    color: rgba(197, 60, 94, 1);
}
textarea {
    height: 100px;
    width: 100%;
    border-radius: 2px;
    border: 1px solid gray;
}
.delete-message {
    cursor: pointer;
}
</style>


